import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';

/**
 * OffersSection - Promotional offers and discounts showcase
 * Features:
 * - Full viewport height
 * - Featured offer highlight
 * - Countdown timer (optional)
 * - Multiple offer cards
 */

// Offers configuration - easily editable
const featuredOffer = {
  title: 'Season Sale',
  subtitle: 'Up to 30% Off',
  description: 'Exclusive discounts on selected luxury timepieces. Limited time offer.',
  image: '/Icons and Logos/HomePageMaterials/Landing Images/WBP201B.BA0632_USP1.jpg',
  link: '/store?sort=price-asc',
  badge: 'Limited Time',
};

const additionalOffers = [
  {
    title: 'New Arrivals',
    description: 'Be the first to discover our latest collection',
    icon: (
      <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
      </svg>
    ),
    link: '/store',
    color: 'from-blue-500/20 to-blue-600/5',
  },
  {
    title: 'Free Shipping',
    description: 'Complimentary delivery on orders over 500,000 IQD',
    icon: (
      <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M5 8h14M5 8a2 2 0 110-4h14a2 2 0 110 4M5 8v10a2 2 0 002 2h10a2 2 0 002-2V8m-9 4h4" />
      </svg>
    ),
    link: '/store',
    color: 'from-green-500/20 to-green-600/5',
  },
  {
    title: 'Warranty Plus',
    description: 'Extended warranty available on all premium watches',
    icon: (
      <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M9 12l2 2 4-4m5.618-4.016A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
      </svg>
    ),
    link: '/about',
    color: 'from-purple-500/20 to-purple-600/5',
  },
];

const OfferCard = ({ offer, index, basePath = '' }) => {
  return (
    <Link
      to={`${basePath}${offer.link}`}
      className="group relative p-6 border border-white/10 bg-white/5 backdrop-blur-sm overflow-hidden transition-all duration-300 hover:border-amber-400/50 hover:bg-white/10"
      style={{ animationDelay: `${index * 100}ms` }}
    >
      {/* Gradient Background */}
      <div className={`absolute inset-0 bg-gradient-to-br ${offer.color} opacity-0 group-hover:opacity-100 transition-opacity duration-300`} />
      
      {/* Content */}
      <div className="relative z-10">
        <div className="text-amber-400 mb-4">{offer.icon}</div>
        <h4 className="text-lg font-light text-white mb-2">{offer.title}</h4>
        <p className="text-white/50 text-sm leading-relaxed">{offer.description}</p>
      </div>

      {/* Arrow */}
      <div className="absolute bottom-6 right-6 text-white/30 group-hover:text-amber-400 transition-colors duration-300">
        <svg className="w-5 h-5 transform group-hover:translate-x-1 transition-transform duration-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M17 8l4 4m0 0l-4 4m4-4H3" />
        </svg>
      </div>
    </Link>
  );
};

const OffersSection = () => {
  const [timeLeft, setTimeLeft] = useState({
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  });
  const location = useLocation();
  const isFibMode = location.pathname.startsWith('/fib');
  const basePath = isFibMode ? '/fib' : '';

  // Countdown timer (set end date as needed)
  useEffect(() => {
    const endDate = new Date();
    endDate.setDate(endDate.getDate() + 7); // 7 days from now

    const timer = setInterval(() => {
      const now = new Date().getTime();
      const distance = endDate.getTime() - now;

      if (distance > 0) {
        setTimeLeft({
          days: Math.floor(distance / (1000 * 60 * 60 * 24)),
          hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
          minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
          seconds: Math.floor((distance % (1000 * 60)) / 1000),
        });
      }
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const TimeBlock = ({ value, label }) => (
    <div className="text-center">
      <div className="text-3xl md:text-4xl font-light text-white mb-1">{String(value).padStart(2, '0')}</div>
      <div className="text-xs text-white/40 tracking-widest uppercase">{label}</div>
    </div>
  );

  return (
    <section className="relative min-h-screen w-full bg-black flex flex-col lg:flex-row overflow-hidden">
      {/* Featured Offer - Left Side */}
      <div className="relative flex-1 min-h-[60vh] lg:min-h-screen overflow-hidden">
        {/* Background Image */}
        <div className="absolute inset-0">
          <img
            src={featuredOffer.image}
            alt={featuredOffer.title}
            className="w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-r from-black via-black/70 to-transparent" />
          <div className="absolute inset-0 bg-gradient-to-t from-black/80 via-transparent to-black/30" />
        </div>

        {/* Content */}
        <div className="relative z-10 h-full flex flex-col justify-end p-8 md:p-12 lg:p-16">
          {/* Badge */}
          <span className="inline-block px-4 py-1 bg-amber-400 text-black text-xs tracking-widest uppercase mb-6 w-fit">
            {featuredOffer.badge}
          </span>

          <p className="text-amber-400 text-sm tracking-[0.2em] uppercase mb-2">{featuredOffer.title}</p>
          <h2
            className="text-5xl md:text-6xl lg:text-7xl font-light text-white mb-4 tracking-tight"
            style={{ fontFamily: "'Cormorant Garamond', serif" }}
          >
            {featuredOffer.subtitle}
          </h2>
          <p className="text-white/60 max-w-md mb-8 leading-relaxed">{featuredOffer.description}</p>

          {/* Countdown */}
          <div className="flex gap-6 md:gap-8 mb-8">
            <TimeBlock value={timeLeft.days} label="Days" />
            <div className="text-white/20 text-2xl">:</div>
            <TimeBlock value={timeLeft.hours} label="Hours" />
            <div className="text-white/20 text-2xl">:</div>
            <TimeBlock value={timeLeft.minutes} label="Mins" />
            <div className="text-white/20 text-2xl">:</div>
            <TimeBlock value={timeLeft.seconds} label="Secs" />
          </div>

          <Link
            to={`${basePath}${featuredOffer.link}`}
            className="inline-flex items-center gap-3 bg-white text-black px-8 py-4 text-sm font-medium tracking-widest uppercase transition-all duration-300 hover:bg-amber-400 w-fit"
          >
            Shop Now
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
            </svg>
          </Link>
        </div>
      </div>

      {/* Additional Offers - Right Side */}
      <div className="lg:w-96 xl:w-[450px] bg-neutral-950 flex flex-col justify-center p-8 lg:p-12 gap-4">
        <div className="mb-6">
          <p className="text-amber-400 text-xs tracking-[0.3em] uppercase mb-2">More Benefits</p>
          <h3 className="text-2xl font-light text-white">Why Choose Us</h3>
        </div>

        {additionalOffers.map((offer, index) => (
          <OfferCard key={index} offer={offer} index={index} basePath={basePath} />
        ))}
      </div>
    </section>
  );
};

export default OffersSection;


