import React, { useState, useEffect } from 'react';
import SimplePriceSlider from './SimplePriceSlider';

// Hardcoded filter options
const filterOptions = {
  brands: [
    "Aerowatch", "Anne Klein", "Balmain", "Bigotti", "Boss", "Calvin Klein", "Casio", 
    "Casio Edifice", "Casio G-Shock", "Casio PROTREK", "Casio Sheen", "Certina", 
    "Citizen", "Cornavin", "Daniel Klein", "Emporio armani", "Festina", "Freelook", 
    "Hislon", "Jovial", "Lamy", "Longines", "Lotus","Lotus-Bracelet", "Mathey-Tissot", "Rhythm", 
    "Seiko", "Swiss Military","Sergio Tacchini", "Tag Heuer", "Tissot", "Tommy Hilfiger", "US-Polo", 
    "Versus Versace","Versace", "Victorinox"
  ],
  gender: ["Man", "Woman", "Unisex"],
  origin: ["Japan", "Spain", "Germany", "Switzerland", "USA"],
  movement: ["Quartz", "Automatic"],
  category: ["BRACELET", "Table Clock", "Wall Clock", "Hand Watch", "Pen"]
};

const initialFilterState = {
  brands: [],
  gender: [],
  origin: [],
  movement: [],
  priceRange: { min: 0, max: 100000000 },
  category: []
};

// Sub-component for collapsible sections
const CollapsibleSection = ({ title, children }) => {
  const [isOpen, setIsOpen] = useState(true);
  return (
    <div className="py-2 border-b">
      <button
        onClick={() => setIsOpen(!isOpen)}
        className="w-full flex justify-between items-center text-left font-semibold"
      >
        <span>{title}</span>
        <span>{isOpen ? '' : '+'}</span>
      </button>
      {isOpen && <div className="mt-2">{children}</div>}
    </div>
  );
};

const Filter = ({ activeFilters, onFilterChange, onModalClose }) => {
  const [localFilters, setLocalFilters] = useState(activeFilters);
  const [sliderMax, setSliderMax] = useState(1000000);

  useEffect(() => {
    setLocalFilters(activeFilters);
  }, [activeFilters]);

  const handleCheckboxChange = (filterType, value) => {
    const currentValues = localFilters[filterType] || [];
    const newValues = currentValues.includes(value)
      ? currentValues.filter(item => item !== value)
      : [...currentValues, value];
    
    setLocalFilters({ ...localFilters, [filterType]: newValues });
  };

  const handlePriceChange = (newPriceRange) => {
    const [min, max] = newPriceRange;
    if (max === 1000000) {
      setSliderMax(10000000);
    }
    setLocalFilters({
      ...localFilters,
      priceRange: { min, max },
    });
  };

  const handleApply = () => {
    onFilterChange(localFilters);
    if (onModalClose) onModalClose();
  };

  const handleClear = () => {
    setLocalFilters(initialFilterState);
    onFilterChange(initialFilterState);
    setSliderMax(1000000);
    if (onModalClose) onModalClose();
  };

  return (
    <div className="filter-sidebar flex flex-col pb-8">
      <div className="flex justify-between items-center mb-4 flex-shrink-0">
        <h3 className="text-xl font-bold">Filters</h3>
        {onModalClose && (
          <button onClick={onModalClose} className="text-2xl">&times;</button>
        )}
      </div>
      
      <div className="flex-grow">
        <CollapsibleSection title="Price">
          <SimplePriceSlider
            min={0}
            max={localFilters.priceRange.max === 10000000 ? 10000000 : sliderMax}
            values={[localFilters.priceRange.min, localFilters.priceRange.max]}
            onChange={handlePriceChange}
          />
        </CollapsibleSection>

        <CollapsibleSection title="Brand">
          <div className="grid grid-cols-2 gap-x-4">
            {filterOptions.brands.map(brand => (
              <label key={brand} className="flex items-center space-x-2">
                <input
                  type="checkbox"
                  checked={(localFilters.brands || []).includes(brand)}
                  onChange={() => handleCheckboxChange('brands', brand)}
                />
                <span>{brand}</span>
              </label>
            ))}
          </div>
        </CollapsibleSection>

        <CollapsibleSection title="Gender">
          {filterOptions.gender.map(gender => (
            <label key={gender} className="flex items-center space-x-2">
              <input
                type="checkbox"
                checked={(localFilters.gender || []).includes(gender)}
                onChange={() => handleCheckboxChange('gender', gender)}
              />
              <span>{gender}</span>
            </label>
          ))}
        </CollapsibleSection>
        
        <CollapsibleSection title="Origin">
          {filterOptions.origin.map(origin => (
            <label key={origin} className="flex items-center space-x-2">
              <input
                type="checkbox"
                checked={(localFilters.origin || []).includes(origin)}
                onChange={() => handleCheckboxChange('origin', origin)}
              />
              <span>{origin}</span>
            </label>
          ))}
        </CollapsibleSection>

        <CollapsibleSection title="Movement">
          {filterOptions.movement.map(movement => (
            <label key={movement} className="flex items-center space-x-2">
              <input
                type="checkbox"
                checked={(localFilters.movement || []).includes(movement)}
                onChange={() => handleCheckboxChange('movement', movement)}
              />
              <span>{movement}</span>
            </label>
          ))}
        </CollapsibleSection>
          
          <CollapsibleSection title="Category">
          {filterOptions.category.map(category => (
            <label key={category} className="flex items-center space-x-2">
              <input
                type="checkbox"
                checked={(localFilters.category || []).includes(category)}
                onChange={() => handleCheckboxChange('category', category)}
              />
              <span>{category}</span>
            </label>
          ))}
        </CollapsibleSection>
      </div>

      <div className="flex-shrink-0 pt-4 mt-auto border-t">
        <div className="flex space-x-2">
          <button
            onClick={handleClear}
            className="w-1/2 py-2 px-4 border border-black text-black bg-white rounded-md hover:bg-gray-100"
          >
            Clear
         </button>
         <button
           onClick={handleApply}
           className="w-1/2 py-2 px-4 bg-black text-white rounded-md hover:bg-gray-800"
         >
           Apply
         </button>
       </div>
      </div>
    </div>
  );
};

export default Filter;