import React, { useState, useEffect, useRef } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { CircleX } from 'lucide-react';

/**
 * Header Component
 * 
 * Features:
 * - Fixed position with hide on scroll down
 * - Centered logo
 * - Navigation links on the LEFT
 * - Icons (search, profile, cart) on the RIGHT
 * - Store dropdown with Men/Women
 * - Expandable search on hover
 * - Amber accent styling to match site theme
 */

const navLinks = [
  { name: 'Home', path: '/' },
  { name: 'Store', path: '/store', hasDropdown: true },
  { name: 'About', path: '/about' },
  { name: 'Contact', path: '/contact' },
];

const storeDropdownItems = [
  { name: 'Men', path: '/store?gender=Man%2CUnisex' },
  { name: 'Women', path: '/store?gender=Woman%2CUnisex' },
];

function Header() {
  const [isMobileOpen, setIsMobileOpen] = useState(false);
  const [showHeader, setShowHeader] = useState(true);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [isScrolled, setIsScrolled] = useState(false);
  const [isSearchExpanded, setIsSearchExpanded] = useState(false);
  const [searchQuery, setSearchQuery] = useState('');
  const [showStoreDropdown, setShowStoreDropdown] = useState(false);
  const searchInputRef = useRef(null);
  const location = useLocation();
  const navigate = useNavigate();

  // Effect for controlling header visibility and background on scroll
  useEffect(() => {
    const controlHeaderVisibility = () => {
      if (typeof window !== "undefined") {
        const currentScrollY = window.scrollY;
        setShowHeader(currentScrollY < lastScrollY || currentScrollY < 100);
        setIsScrolled(currentScrollY > 50);
        setLastScrollY(currentScrollY);
      }
    };

    window.addEventListener("scroll", controlHeaderVisibility);
    return () => window.removeEventListener("scroll", controlHeaderVisibility);
  }, [lastScrollY]);

  // Lock and unlock body scroll based on the mobile menu state
  useEffect(() => {
    if (isMobileOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "visible";
    }
  }, [isMobileOpen]);

  // Close mobile menu on route change
  useEffect(() => {
    setIsMobileOpen(false);
    setIsSearchExpanded(false);
    setShowStoreDropdown(false);
  }, [location.pathname]);

  // Focus search input when expanded
  useEffect(() => {
    if (isSearchExpanded && searchInputRef.current) {
      searchInputRef.current.focus();
    }
  }, [isSearchExpanded]);

  const isActive = (path) => {
    if (path === '/') {
      return location.pathname === '/' || location.pathname === '/fib';
    }
    if (path === '/store') {
      return location.pathname === '/store' || location.pathname === '/fib/store' || location.pathname.includes('/store');
    }
    return location.pathname === path || location.pathname === `/fib${path}`;
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    if (searchQuery.trim()) {
      window.location.href = `#/store?q=${encodeURIComponent(searchQuery)}`;
    }
  };

  const shouldShowBackButton = () => {
    // Show back button on all pages except homepage
    return location.pathname !== '/' && location.pathname !== '/fib';
  };

  const clearSearch = () => {
    setSearchQuery('');
    if (searchInputRef.current) searchInputRef.current.blur();
  };

  return (
    <header
      className={`fixed w-full ${isMobileOpen ? 'z-[51]' : 'z-50'} top-0 left-0 transition-all duration-300 ease-in-out ${
        showHeader ? "translate-y-0" : "-translate-y-full"
      } ${
        isScrolled
          ? "bg-neutral-950/95 backdrop-blur-md shadow-lg"
          : "bg-gradient-to-b from-black/80 to-transparent"
      }`}
    >
      {/* Main Header Content */}
      <div className="container mx-auto px-6 md:px-12 lg:px-24">
        <div className="flex items-center justify-between py-5 md:py-4">
          
          {/* LEFT: Mobile Back Button + Mobile Menu Button + Desktop Navigation */}
          <div className="flex items-center flex-shrink-0 min-w-[40px]">
            {/* Mobile Back Button (shown on all pages except homepage) */}
            {shouldShowBackButton() && (
              <button
                onClick={() => navigate(-1)}
                className="relative z-[60] w-10 h-10 flex items-center justify-center md:hidden group mr-1"
                aria-label="Go back"
              >
                <svg
                  className="w-6 h-6 text-white/80 group-hover:text-white transition-colors duration-200"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M15 19l-7-7 7-7"
                  />
                </svg>
              </button>
            )}

            {/* Mobile Menu Button */}
            <button
              onClick={() => setIsMobileOpen(!isMobileOpen)}
              className="relative z-[60] w-10 h-10 flex items-center justify-center md:hidden group"
              aria-label="Toggle menu"
            >
              <div className="flex flex-col justify-center items-center w-6 h-6">
                <span
                  className={`block h-0.5 w-6 bg-white transition-all duration-300 ${
                    isMobileOpen ? 'rotate-45 translate-y-1' : ''
                  }`}
                />
                <span
                  className={`block h-0.5 w-6 bg-white my-1 transition-all duration-300 ${
                    isMobileOpen ? 'opacity-0' : ''
                  }`}
                />
                <span
                  className={`block h-0.5 w-6 bg-white transition-all duration-300 ${
                    isMobileOpen ? '-rotate-45 -translate-y-2' : ''
                  }`}
                />
              </div>
            </button>

            {/* Desktop Navigation - Left Side */}
            <nav className="hidden md:flex items-center gap-1">
              {navLinks.map((link) => (
                <div 
                  key={link.name} 
                  className="relative"
                  onMouseEnter={() => link.hasDropdown && setShowStoreDropdown(true)}
                  onMouseLeave={() => link.hasDropdown && setShowStoreDropdown(false)}
                >
                  <Link
                    to={link.path}
                    className={`relative px-4 py-2 text-sm tracking-wide transition-colors duration-300 group flex items-center gap-1 ${
                      isActive(link.path) 
                        ? 'text-amber-400' 
                        : 'text-white/80 hover:text-white'
                    }`}
                  >
                    {link.name}
                    {link.hasDropdown && (
                      <svg 
                        className={`w-3 h-3 transition-transform duration-200 ${showStoreDropdown ? 'rotate-180' : ''}`} 
                        fill="none" 
                        stroke="currentColor" 
                        viewBox="0 0 24 24"
                      >
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                      </svg>
                    )}
                    {/* Underline indicator */}
                    <span 
                      className={`absolute bottom-0 left-1/2 -translate-x-1/2 h-px bg-amber-400 transition-all duration-300 ${
                        isActive(link.path) ? 'w-4' : 'w-0 group-hover:w-4'
                      }`} 
                    />
                  </Link>

                  {/* Store Dropdown */}
                  {link.hasDropdown && (
                    <div 
                      className={`absolute top-full left-0 pt-2 transition-all duration-200 ${
                        showStoreDropdown ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
                      }`}
                    >
                      <div className="bg-neutral-900/95 backdrop-blur-md border border-white/10 rounded-lg py-2 min-w-[120px] shadow-xl">
                        {storeDropdownItems.map((item) => (
                          <Link
                            key={item.name}
                            to={item.path}
                            className="block px-4 py-2 text-sm text-white/70 hover:text-amber-400 hover:bg-white/5 transition-colors duration-200"
                          >
                            {item.name}
                          </Link>
                        ))}
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </nav>
          </div>

          {/* CENTER: Logo */}
          <div className="absolute left-1/2 transform -translate-x-1/2 px-12 sm:px-0">
            <Link to="/" className="block">
              <img
                src="/Icons and Logos/Logo.png"
                alt="Watches House"
                className="h-9 sm:h-11 md:h-12 w-auto transition-transform duration-300 hover:scale-105"
              />
            </Link>
          </div>

          {/* RIGHT: Icons */}
          <div className="flex items-center gap-2 md:gap-1 flex-shrink-0 min-w-[40px]">
            {/* Desktop Icons */}
            <div className="hidden md:flex items-center gap-1">
              {/* Search */}
              <div 
                className="relative"
                onMouseEnter={() => setIsSearchExpanded(true)}
                onMouseLeave={() => !searchQuery && setIsSearchExpanded(false)}
              >
                <form onSubmit={handleSearchSubmit} className="flex items-center">
                  <div 
                    className={`flex items-center overflow-hidden transition-all duration-300 ease-out ${
                      isSearchExpanded ? 'w-48 bg-white/10 rounded-full' : 'w-10'
                    }`}
                  >
                    <button
                      type="submit"
                      className="flex-shrink-0 w-10 h-10 flex items-center justify-center text-white/70 hover:text-amber-400 transition-colors duration-200"
                      aria-label="Search"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                      </svg>
                    </button>
                    <input
                      ref={searchInputRef}
                      type="text"
                      placeholder="Search..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      className={`bg-transparent text-white text-sm placeholder-white/40 outline-none transition-all duration-300 ${
                        isSearchExpanded ? 'w-full pr-4 opacity-100' : 'w-0 opacity-0'
                      }`}
                    />
                    {searchQuery && (
                      <button
                        type="button"
                        onClick={clearSearch}
                        className="flex-shrink-0 w-8 h-8 flex items-center justify-center text-white/70 hover:text-amber-400 transition-colors duration-200"
                        aria-label="Clear search"
                      >
                        <CircleX className="w-5 h-5" />
                      </button>
                    )}
                  </div>
                </form>
              </div>

              {/* Profile Icon */}
              <Link
                to="/about"
                className="w-10 h-10 flex items-center justify-center text-white/70 hover:text-amber-400 transition-colors duration-200"
                aria-label="Profile"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M16 7a4 4 0 11-8 0 4 4 0 018 0zM12 14a7 7 0 00-7 7h14a7 7 0 00-7-7z" />
                </svg>
              </Link>

              {/* Cart Icon */}
              <Link
                to="/store"
                className="w-10 h-10 flex items-center justify-center text-white/70 hover:text-amber-400 transition-colors duration-200 relative"
                aria-label="Cart"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z" />
                </svg>
              </Link>
            </div>

            {/* Mobile Icons */}
            <div className="flex items-center gap-2 md:hidden">
              <Link
                to="/store"
                className="w-10 h-10 flex items-center justify-center text-white/70 active:text-amber-400"
                aria-label="Cart"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z" />
                </svg>
              </Link>
            </div>
          </div>
        </div>
      </div>

      {/* Mobile Menu Overlay */}
      <div
        className={`fixed inset-0 z-50 md:hidden ${
          isMobileOpen
            ? 'pointer-events-auto'
            : 'pointer-events-none'
        }`}
      >
        {/* Content with background */}
        <div
          className="relative z-10 min-h-screen flex flex-col items-center justify-center gap-6 px-6 pt-20 pb-8 transition-all duration-500 bg-neutral-950/95 backdrop-blur-md"
          style={{
            opacity: isMobileOpen ? 1 : 0
          }}
        >

          {/* Mobile Search */}
          <form
            onSubmit={handleSearchSubmit}
            className="w-full max-w-xs flex-shrink-0"
            style={{
              opacity: isMobileOpen ? 1 : 0,
              transition: 'opacity 0.3s ease 0.1s'
            }}
          >
            <div className="relative">
              <input
                type="text"
                placeholder="Search watches..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="w-full bg-white/10 border border-white/20 rounded-full px-5 py-3 text-white text-sm placeholder-white/40 outline-none focus:border-amber-400/50 transition-colors"
              />
                {searchQuery && (
                  <button
                    type="button"
                    onClick={clearSearch}
                    className="absolute right-10 top-1/2 -translate-y-1/2 text-white/70 hover:text-amber-400 transition-colors"
                    aria-label="Clear search"
                  >
                    <CircleX className="w-4 h-4"/>
                  </button>
                )}
              <button
                type="submit"
                className="absolute right-3 top-1/2 -translate-y-1/2 text-white/50 hover:text-amber-400 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
              </button>
            </div>
          </form>

          {/* Navigation Links */}
          <nav className="flex flex-col items-center gap-2 flex-shrink-0">
            {navLinks.map((link, index) => (
              <div key={link.name} className="text-center">
                <Link
                  to={link.path}
                  onClick={() => setIsMobileOpen(false)}
                  className={`relative px-8 py-4 text-2xl font-light tracking-wide transition-all duration-300 block ${
                    isActive(link.path) 
                      ? 'text-amber-400' 
                      : 'text-white/70 hover:text-white'
                  }`}
                  style={{ 
                    fontFamily: "'Cormorant Garamond', serif",
                    transitionDelay: isMobileOpen ? `${index * 50}ms` : '0ms',
                    transform: isMobileOpen ? 'translateY(0)' : 'translateY(20px)',
                    opacity: isMobileOpen ? 1 : 0,
                  }}
                >
                  {link.name}
                  {isActive(link.path) && (
                    <span className="absolute left-0 top-1/2 -translate-y-1/2 w-1 h-6 bg-amber-400 rounded-full" />
                  )}
                </Link>
                {/* Mobile Store Sub-links */}
                {link.hasDropdown && (
                  <div 
                    className="flex gap-6 justify-center mt-1"
                    style={{ 
                      transitionDelay: isMobileOpen ? `${(index + 0.5) * 50}ms` : '0ms',
                      opacity: isMobileOpen ? 1 : 0,
                    }}
                  >
                    {storeDropdownItems.map((item) => (
                      <Link
                        key={item.name}
                        to={item.path}
                        onClick={() => setIsMobileOpen(false)}
                        className="text-white/50 text-sm hover:text-amber-400 transition-colors"
                      >
                        {item.name}
                      </Link>
                    ))}
                  </div>
                )}
              </div>
            ))}
          </nav>

          {/* Contact Info */}
          <div
            className="mt-8 text-center transition-all duration-500 flex-shrink-0"
            style={{
              transitionDelay: isMobileOpen ? '200ms' : '0ms',
              opacity: isMobileOpen ? 1 : 0,
            }}
          >
            <p className="text-amber-400/60 text-xs tracking-[0.2em] uppercase mb-2">Get in Touch</p>
            <a 
              href="tel:+9647511151003" 
              className="text-white/60 text-sm hover:text-amber-400 transition-colors"
            >
              +(964) 751 115 1003
            </a>
          </div>
        </div>
      </div>
    </header>
  );
}

export default Header;