function normalizeFIBUser(apiUser = {}, dbId = null) {
  const {
    name, phoneNumber, dateOfBirth, city, governorate, nationality, iban,
    id, uid, email
  } = apiUser;

  return {
    id: dbId || id || uid || `fib:${phoneNumber || iban || name || "unknown"}`,
    displayName: name || phoneNumber || "User",
    name: name || null,
    phone: phoneNumber || null,
    dateOfBirth: dateOfBirth || null,
    city: city || null,
    governorate: governorate || null,
    nationality: nationality || null,
    iban: iban || null,
    email: email || null,
    _raw: apiUser,
  };
}

