export function removeSplash(el, durationMs = 600) {
  if (!el) return Promise.resolve();
  el.style.setProperty("--fib-splash-duration", `${durationMs}ms`);

  return new Promise((resolve) => {
    try {
      el.classList.remove("enter");
      el.classList.add("leave");

      const onEnd = (e) => {
        if (e.target !== el || e.propertyName !== "opacity") return;
        el.removeEventListener("transitionend", onEnd);
        try { document.getElementById("fib-splash-style")?.remove(); } catch {}
        try { el.remove(); } catch {}
        resolve();
      };
      el.addEventListener("transitionend", onEnd, { once: true });

      setTimeout(() => {
        if (document.body.contains(el)) {
          try { document.getElementById("fib-splash-style")?.remove(); } catch {}
          try { el.remove(); } catch {}
        }
        resolve();
      }, durationMs + 100);
    } catch {
      try { el.remove(); } catch {}
      resolve();
    }
  });
}

function setImgHints(img) {
  img.decoding = "async";
  try { img.fetchPriority = "high"; } catch {}
  img.loading = "eager";
}

// Helper to load image as a promise
function preloadImage(imgElement) {
  return new Promise((resolve) => {
    if (imgElement.complete && imgElement.naturalHeight !== 0) {
      resolve();
    } else {
      imgElement.onload = () => resolve();
      imgElement.onerror = () => resolve(); // Resolve anyway to avoid blocking
    }
  });
}

export async function createSplash() {
  const existing = document.getElementById("fib-splash");
  if (existing) return existing;

  let style = document.getElementById("fib-splash-style");
  if (!style) {
    style = document.createElement("style");
    style.id = "fib-splash-style";
    style.textContent = `
      @keyframes fadein { from { opacity: 0 } to { opacity: 1 } }
      @keyframes pulse   { 0%,100%{opacity:.4} 50%{opacity:1} }

      #fib-splash {
        --fib-splash-duration: 800ms;
        opacity: 0;
        transition: opacity var(--fib-splash-duration) ease;
      }
      #fib-splash.enter { opacity: 1; }
      #fib-splash.leave { opacity: 0; }
      
      #fib-splash .brand { 
        opacity: 0; 
        transform: translateY(6px);
        transition: opacity calc(var(--fib-splash-duration) * 0.8) ease, 
                   transform calc(var(--fib-splash-duration) * 0.8) ease;
        filter: drop-shadow(0 2px 10px rgba(0,0,0,.35));
      }
      #fib-splash.ready .brand { 
        opacity: 1; 
        transform: translateY(0);
      }

      #fib-splash .fade  { animation: fadein .5s ease-out both }
      #fib-splash .pulse { animation: pulse 1.4s ease-in-out infinite }
      
      #fib-splash .vline { 
        width: 2px; 
        background: rgba(255, 255, 255, 0.6);
        height: 60px;
        margin: 0;
        display: block;
      }

      #fib-splash .dot { 
        width: 15px;
        height: 15px;
        background: rgba(255, 255, 255, 0.6);
        border-radius: 50%; 
        margin: 0;
        display: block;
      }

      #fib-splash .arrow {
        margin: 0;
        display: block;
        height: 12px;
      }

      #fib-splash .arrow::before {
        content: '';
        display: block;
        width: 24px;
        height: 12px;
        border-bottom: 2px solid rgba(255, 255, 255, 0.6);
        border-left: 2px solid rgba(255, 255, 255, 0.6);
        border-right: 2px solid rgba(255, 255, 255, 0.6);
        border-top: none;
        border-radius: 0 0 24px 24px;
        margin: 0 auto;
      }

      #fib-splash .caption { color: #d8d8d8; font-size: 14px; margin: 8px 0; }

      #fib-splash .stagger > * {
        opacity: 0; 
        transform: translateY(6px);
        transition: opacity calc(var(--fib-splash-duration) * 0.8) ease, 
                   transform calc(var(--fib-splash-duration) * 0.8) ease;
      }
      #fib-splash.ready .stagger > * { opacity: 1; transform: translateY(0); }
      
      #fib-splash.ready .brand:nth-of-type(1) { transition-delay: .0s; } 
      #fib-splash.ready .stagger > *:nth-child(1){ transition-delay: .1s; } 
      #fib-splash.ready .stagger > *:nth-child(2){ transition-delay: .15s; } 
      #fib-splash.ready .stagger > *:nth-child(3){ transition-delay: .2s; } 
      #fib-splash.ready .stagger > *:nth-child(4){ transition-delay: .25s; } 
      #fib-splash.ready .stagger > *:nth-child(5){ transition-delay: .3s; } 
      #fib-splash.ready .brand:nth-of-type(2) { transition-delay: .35s; } 
    `;
    document.head.appendChild(style);
  }

  const el = document.createElement("div");
  el.id = "fib-splash";
  Object.assign(el.style, {
    position: "fixed", inset: "0", background: "#0d0f12", color: "#f7f7f7",
    display: "flex", flexDirection: "column", alignItems: "center", justifyContent: "center",
    textAlign: "center", zIndex: 99999, fontFamily: "system-ui, -apple-system, sans-serif",
  });

  // --- Top Logo ---
  const top = document.createElement("img");
  top.src = "/Icons%20and%20Logos/fibLogo.png";
  top.alt = "First Iraqi Bank";
  setImgHints(top);
  Object.assign(top.style, { width: "140px", marginBottom: "16px" });
  top.className = "brand";
  el.appendChild(top);

  const mid = document.createElement("div");
  mid.className = "stagger";
  Object.assign(mid.style, { display: "flex", flexDirection: "column", alignItems: "center", margin: "8px 0" });

  ["dot pulse","vline fade","caption fade","vline fade","arrow pulse"].forEach(cls => {
    const n = document.createElement("div");
    n.className = cls;
    if (cls.startsWith("caption")) n.textContent = "On your way to...";
    mid.appendChild(n);
  });
  el.appendChild(mid);

  // --- Bottom Logo ---
  const bottom = document.createElement("img");
  bottom.src = "/Icons%20and%20Logos/LogoName.png";
  bottom.alt = "Watch House";
  setImgHints(bottom);
  Object.assign(bottom.style, { width: "120px", marginTop: "16px" });
  bottom.className = "brand";
  el.appendChild(bottom);

  document.body.appendChild(el);

  // --- WAIT FOR IMAGES before fading in ---
  // This ensures consistency: elements exist but are invisible. 
  // We wait for load, then fade the whole container in.
  await Promise.all([preloadImage(top), preloadImage(bottom)]);

  requestAnimationFrame(() => {
    el.classList.add("enter");
    requestAnimationFrame(() => el.classList.add("ready"));
  });
  
  return el;
}

// --- Helpers ---
const MAX_SSO_ATTEMPTS_PER_DAY = 5;

function fromQS(name) { try { return new URLSearchParams(window.location.search).get(name); } catch { return null; } }
function devBypassEnabled() { return localStorage.getItem("FIB_DEV_BYPASS") === "1" || fromQS("fib_dev") === "1"; }
function setDevBypass() {
  if (fromQS("fib_dev") === "1") {
    localStorage.setItem("FIB_DEV_BYPASS", "1");
    localStorage.setItem("FIB_DEV_NAME", fromQS("name") || "Dev User");
  }
}
function devName() { return localStorage.getItem("FIB_DEV_NAME") || "Dev User"; }
function getBridge() {
  return (typeof window !== "undefined" && window.FIBNativeBridge && window.FIBNativeBridge.isFIBBridgeAvailable)
    ? window.FIBNativeBridge : null;
}

export default function MiniAppLayout() {
  const { isAuthenticated, expired, setSession, canAttemptSSO, recordSSOAttempt } = useFIB();
  
  // Controls if the actual App content (Outlet) is rendered.
  // We default to FALSE to prevent any early rendering/flashing of the dashboard.
  const [isAppReady, setIsAppReady] = useState(false);
  
  const [blocked, setBlocked] = useState(false);
  const [error, setError] = useState("");

  const splashElRef = useRef(null);
  const splashT0 = useRef(Date.now());
  const cancelRef = useRef(false);
  const pollTimerRef = useRef(null);

  const nav = useNavigate();
  const loc = useLocation();

  // Use LayoutEffect to inject Splash BEFORE the paint happens.
  useLayoutEffect(() => {
    // Fire and forget - createSplash is async (waits for images) 
    // but the DIV is appended synchronously at opacity 0 first.
    createSplash().then(el => {
      splashElRef.current = el;
      // Reset start time to when images actually finished loading for consistent timing
      splashT0.current = Date.now();
    });
    
    setDevBypass();
    
    return () => {
      cancelRef.current = true;
      if (pollTimerRef.current) clearInterval(pollTimerRef.current);
      if (splashElRef.current) removeSplash(splashElRef.current);
      const style = document.getElementById("fib-splash-style");
      if (style && style.parentNode) style.parentNode.removeChild(style);
    };
  }, []);

  async function finishSplash() {
    await removeSplash(splashElRef.current);
    // Only now do we allow the React App to render its content
    setIsAppReady(true);
  }

  useEffect(() => {
    let cancel = false;
    let pollTimer = null;
    let pollingStarted = false;

    const finishSplashSafe = async () => { try { await finishSplash(); } catch {} };
    const stopPolling = async (msg) => {
      if (pollTimer) { clearInterval(pollTimer); pollTimer = null; }
      if (msg) setError(msg);
      pollingStarted = false;
      await finishSplashSafe();
    };

    const startVerifyPollingByCode = (code, { intervalMs = 1000, maxMs = 120000 } = {}) => { 
      if (pollingStarted || !code) return; 
      pollingStarted = true;

      const t0 = Date.now();
      let inFlight = false;

      pollTimer = setInterval(async () => {
        if (cancel || inFlight) return;
        if (Date.now() - t0 > maxMs) return stopPolling("Authorization timed out.");

        inFlight = true;
        try {
          const res = await verifySSO({ code });   
          if (!res.ok) {
            if (res.status === 401) return; 
            if ([403,410,429].includes(res.status)) return stopPolling(res.message || "Access denied. Contact support.");
            return; 
          }

          const rawUser = res.data;
          const token = rawUser.token || null; 

          if (rawUser && rawUser.name) {
            clearInterval(pollTimer); pollTimer = null; pollingStarted = false;
            
            // const { id: dbUuid } = await getOrCreateUser(supabase, rawUser);
            const user = normalizeFIBUser(rawUser, null);
            
            setSession({ user, token });
            
            if (!loc.pathname.startsWith("/fib")) nav("/fib", { replace: true });
            await finishSplashSafe();
          }
        } finally { inFlight = false; }
      }, intervalMs);
    };

    (async () => {
      // 0) Reuse existing app session
      if (isAuthenticated && !expired) {
        // --- DELAY LOGIC FOR EXISTING USER ---
        // If the user is already signed in, we want to ensure the splash 
        // stays up for at least 1 second so it doesn't just flicker.
        const MIN_EXISTING_USER_DELAY = 1000;
        
        // Wait for splash element to be fully initialized (images loaded) if it isn't yet
        while (!splashElRef.current) {
          await new Promise(r => setTimeout(r, 50));
        }

        const elapsed = Date.now() - splashT0.current;
        const remaining = Math.max(0, MIN_EXISTING_USER_DELAY - elapsed);
        
        if (remaining > 0) {
            await new Promise(resolve => setTimeout(resolve, remaining));
        }

        await finishSplashSafe();
        return; 
      }

      // 1) Dev bypass
      if (devBypassEnabled()) {
        const exp = Math.floor(Date.now()/1000) + 2*60*60;
        setSession({ user: { id:"00000000-0000-0000-0000-000000000000", displayName: devName(), email:"dev@example.com" }, token: { value:"dev-token", exp } });
        if (!loc.pathname.startsWith("/fib")) nav("/fib", { replace:true });
        await finishSplashSafe();
        return;
      }

      // 2) Must be inside the bank app
      const bridge = getBridge();
      if (!bridge) {
        setError("Please open this mini-app from inside the bank app.");
        await finishSplashSafe();
        return;
      }

      // 3) INITIATE  CODE
      const state = { code: null, ridDashless: null };

      const onAuthOk = async () => {
        await new Promise(resolve => setTimeout(resolve, 500));
        
        let res = await verifySSO({ code: state.code });
        
        if (!res.ok && res.status === 401) {
            await new Promise(resolve => setTimeout(resolve, 800));
            res = await verifySSO({ code: state.code });
        }
        
        if (!res.ok) {
          if ([401,403,410,429].includes(res.status)) 
            return stopPolling(res.message || "Access denied. Contact support.");
          return;
        }
        
        const rawUser = res.data;
        const token = rawUser.token || null;

        if (rawUser && rawUser.name) {
            // const { id: dbUuid } = await getOrCreateUser(supabase, rawUser);
            const user = normalizeFIBUser(rawUser, null);
            
            setSession({ user, token });
            
            if (!loc.pathname.startsWith("/fib")) nav("/fib", { replace: true });
            await finishSplashSafe();
        }
      };

      const onAuthFail = async () => stopPolling("Native authentication failed.");

      bridge.addEventListener?.("AUTHENTICATED", onAuthOk);
      bridge.addEventListener?.("AUTHENTICATION_FAILED", onAuthFail);

      try {
        const init = await initiateSSO();   
        recordSSOAttempt();  
        state.code = init.code;
        if (!state.code) throw new Error("Missing code from initiate");
        state.ridDashless = String(state.code).replace(/-/g, "");

        bridge.sendMessage?.({ type: "AUTHENTICATE", body: { readableId: state.ridDashless } });

        setTimeout(() => {
          if (!cancel) startVerifyPollingByCode(state.code, { intervalMs: 1000, maxMs: 120000 });
        }, 3500);
      } catch (e) {
        setError(`Access issue  ${e?.message || e}. Contact support.`);
        await finishSplashSafe();
      }
    })();

    return () => {
      cancel = true;
      if (pollTimer) clearInterval(pollTimer);
    };
  }, []);

  const showIssue = blocked || error;
  if (showIssue) {
    return (
      <div className="min-h-screen flex items-center justify-center p-6">
        <div className="max-w-md w-full border rounded-2xl p-6 shadow-lg bg-white">
          <h1 className="text-xl font-semibold mb-2 text-red-600">Access Issue</h1>
          <p className="text-sm text-gray-600">{error || "SSO unavailable. Contact support."}</p>
        </div>
      </div>
    );
  }

  // Prevent ANY render of the Outlet until the splash has actually finished.
  // This solves the split-second render issue.
  return (
    <div className="min-h-screen">
       {isAppReady && <Outlet />}
    </div>
  );
}