import React, { useState, useEffect, useCallback, useRef, useContext } from "react";
import ProductCard from "../components/ProductCard";
import Filter from "../components/Filter";
import { StoreContext } from "../contexts/StoreContext";
import { ProductContext, ProductProvider } from "../contexts/ProductContext";


function StorePage() {
  const {
    products,
    totalProducts,
    page,
    setPage,
    hasMore,
    searchParams,
    setSearchParams,
    loadProducts,
    loading,
    error,
    getFiltersFromParams,
    scrollPosition,
    setScrollPosition,
    setProducts,
    setHasMore,
    searchQuery,
    setSearchQuery
  } = useContext(StoreContext);

  const [isFilterModalOpen, setIsFilterModalOpen] = useState(false);
  const [searchTerm, setSearchTerm] = useState(searchQuery);
  const observer = useRef();
  const loadingRef = useRef(null);

  const activeFilters = getFiltersFromParams();
  const sortOption = searchParams.get('sort') || 'default';

  // Effect to reset products when URL search params change
  useEffect(() => {
    const queryFromUrl = searchParams.get('q') || '';
    setSearchTerm(queryFromUrl);
    setSearchQuery(queryFromUrl);
    setProducts([]);
    setPage(1);
    setHasMore(true);
  }, [searchParams, setProducts, setPage, setHasMore, setSearchQuery]);

  // Effect to fetch data when page, filters, or sort changes
  useEffect(() => {
    if (hasMore || page === 1) {
      loadProducts(page);
    }
  }, [page, searchParams, hasMore, loadProducts]);

  // Effect to restore scroll position
  useEffect(() => {
    if (scrollPosition) {
      window.scrollTo(0, scrollPosition);
    }
  }, []);

  // Effect to save scroll position on unmount
  useEffect(() => {
    return () => {
      setScrollPosition(window.scrollY);
    };
  }, [setScrollPosition]);

  // Intersection Observer for infinite scroll
  useEffect(() => {
    const handleObserver = (entries) => {
      const target = entries[0];
      if (target.isIntersecting && hasMore && !loading) {
        setPage(prevPage => prevPage + 1);
      }
    };

    observer.current = new IntersectionObserver(handleObserver, { rootMargin: '200px' });

    if (loadingRef.current) {
      observer.current.observe(loadingRef.current);
    }

    return () => {
      if (observer.current) {
        observer.current.disconnect();
      }
    };
  }, [hasMore, loading, setPage]);

  // Effect to handle body scroll when filter modal is open
  useEffect(() => {
    if (isFilterModalOpen) {
      document.body.classList.add('overflow-hidden');
    } else {
      document.body.classList.remove('overflow-hidden');
    }
    // Cleanup function to remove the class if the component unmounts
    return () => {
      document.body.classList.remove('overflow-hidden');
    };
  }, [isFilterModalOpen]);
 
   // Handler for when filters are changed in the Filter component
   const handleFilterChange = useCallback((newFilters) => {
    const newSearchParams = new URLSearchParams(searchParams);

    // Handle regular filters
    Object.entries(newFilters).forEach(([key, value]) => {
      if (key !== 'priceRange' && key !== 'searchQuery') {
        if (Array.isArray(value) && value.length > 0) {
          newSearchParams.set(key, value.join(','));
        } else {
          newSearchParams.delete(key);
        }
      }
    });

    // Handle priceRange filter
    const { priceRange } = newFilters;
    if (priceRange && priceRange.min > 0) {
      newSearchParams.set('minPrice', priceRange.min.toString());
    } else {
      newSearchParams.delete('minPrice');
    }

    if (priceRange && priceRange.max < 10000000) {
      newSearchParams.set('maxPrice', priceRange.max.toString());
    } else {
      newSearchParams.delete('maxPrice');
    }

    // Handle search query from filters
    if (newFilters.searchQuery) {
      newSearchParams.set('q', newFilters.searchQuery);
    } else if (!newSearchParams.has('q')) { // Don't delete if it's already in the URL
      newSearchParams.delete('q');
    }

    setSearchParams(newSearchParams, { replace: true });
  }, [searchParams, setSearchParams]);

  // Handler for when sort option changes
  const handleSortChange = useCallback((newSortOption) => {
    const newSearchParams = new URLSearchParams(searchParams);
    if (newSortOption === 'default') {
      newSearchParams.delete('sort');
    } else {
      newSearchParams.set('sort', newSortOption);
    }
    setSearchParams(newSearchParams, { replace: true });
  }, [searchParams, setSearchParams]);

  const handleSearch = () => {
    const newSearchParams = new URLSearchParams(searchParams);
    if (searchTerm) {
      newSearchParams.set('q', searchTerm);
    } else {
      newSearchParams.delete('q');
    }
    if (searchParams.get('q') !== searchTerm) {
      setSearchParams(newSearchParams, { replace: true });
    }
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    handleSearch();
  };

  const handleClearSearch = () => {
    setSearchTerm('');
    const newSearchParams = new URLSearchParams(searchParams);
    newSearchParams.delete('q');
    setSearchParams(newSearchParams, { replace: true });
  };

  const activeFilterCount = Object.values(activeFilters).reduce((count, filter) => {
      if (Array.isArray(filter)) return count + filter.length;
      if (typeof filter === 'object' && (filter.min > 0 || filter.max < 10000000)) return count + 1;
      if (typeof filter === 'string' && filter.length > 0) return count + 1;
      return count;
  }, 0);

  return (
    <div className="container mx-auto px-4 pt-24">
      <div className="flex flex-col lg:flex-row gap-x-6">
        <div className="w-full lg:w-80 flex-shrink-0 hidden lg:block">
          <Filter
            activeFilters={activeFilters}
            onFilterChange={handleFilterChange}
          />
        </div>
        
        <div className="flex-grow">
          <div className="flex justify-between items-center mb-4">
            <div className="text-lg font-semibold text-gray-700">
              {searchQuery && !loading && (
                <div className="text-sm text-gray-600">
                  Showing results for: <strong>'{searchQuery}'</strong>
                </div>
              )}
              {!searchQuery && totalProducts > 0 && !loading && (
                `${totalProducts} product${totalProducts === 1 ? '' : 's'} found`
              )}
            </div>
            <div className="flex items-center gap-4">
              <form onSubmit={handleSearchSubmit} className="relative flex-grow">
                <input
                  type="text"
                  placeholder="Search by model or reference..."
                  className="block w-full pl-2 pr-8 py-1 text-sm border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md shadow-sm"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
                {searchTerm && (
                  <button
                    type="button"
                    onClick={handleClearSearch}
                    className="absolute inset-y-0 right-0 flex items-center pr-3"
                    aria-label="Clear search"
                  >
                    <svg className="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                )}
              </form>
              <div className="flex items-center">
                <label htmlFor="sort-options" className="text-sm font-medium text-gray-700 mr-2 whitespace-nowrap">Sort by:</label>
                <select
                  id="sort-options"
                  className="block w-full pl-2 pr-8 py-1 text-sm border-gray-300 focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm rounded-md shadow-sm"
                  value={sortOption}
                  onChange={(e) => handleSortChange(e.target.value)}
                >
                  <option value="default">Default</option>
                  <option value="price-asc">Price: Low to High</option>
                  <option value="price-desc">Price: High to Low</option>
                  <option value="ref-asc">Ref. Number: A-Z</option>
                  <option value="ref-desc">Ref. Number: Z-A</option>
                </select>
              </div>
            </div>
          </div>
          
          <div className="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-3 gap-4">
            {products.map((product, index) => {
              const outOfStock = (product.muthana_qty || 0) === 0 && (product.royal_qty || 0) === 0 && (product.wh_qty || 0) === 0;
              return (
                <ProductCard 
                  key={`${product.ref_number}-${index}`} 
                  product={{ ...product, outOfStock }}
                  priority={index < 6}
                  isStorePage={true}
                />
              );
            })}
          </div>
          
          <div ref={loadingRef} className="h-10" />

          {loading && page > 1 && <div className="text-center p-4">Loading more products...</div>}
          
          {!loading && products.length === 0 && (
            <div className="text-center p-4 mt-4">
              {searchQuery
                ? `No products found for '${searchQuery}'`
                : "No products found matching your filters."
              }
            </div>
          )}

          {error && <div className="text-center p-4 mt-4 text-red-500">{error}</div>}
        </div>
      </div>

      <div className="lg:hidden fixed bottom-6 left-6 z-30">
        <button
          onClick={() => setIsFilterModalOpen(true)}
          className="relative bg-black text-white p-4 rounded-full shadow-lg"
          aria-label={`Open filters (${activeFilterCount} active)`}
        >
          <img src="/Icons and Logos/settings-sliders.svg" alt="Open Filters" className="invert w-6 h-6" />
          {activeFilterCount > 0 && (
            <span className="absolute -top-1 -right-1 flex h-5 w-5 items-center justify-center rounded-full bg-red-500 text-xs font-bold text-white">
              {activeFilterCount}
            </span>
          )}
        </button>
      </div>

      {isFilterModalOpen && (
        <div className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden" onClick={() => setIsFilterModalOpen(false)}></div>
      )}
      <div className={`fixed bottom-0 left-0 right-0 bg-white shadow-2xl rounded-t-lg p-4 z-50 transform transition-transform duration-300 ease-in-out lg:hidden ${isFilterModalOpen ? 'translate-y-0' : 'translate-y-full'} max-h-[85vh] flex flex-col overflow-y-auto`}>
        <Filter
          activeFilters={activeFilters}
          onFilterChange={handleFilterChange}
          onModalClose={() => setIsFilterModalOpen(false)}
        />
      </div>
    </div>
  );
}

export default StorePage;
