import { supabase } from '../supabaseClient'; 

// --- SSO Functions (Unchanged) ---

export async function initiateSSO() {
  const { data, error } = await supabase.functions.invoke('sso-initiate', {
    method: 'POST',
    body: {}, 
  });

  if (error) {
    throw new Error(`initiate failed: ${error.message || error}`);
  }

  if (!data.code) throw new Error("initiate missing 'code' in response");
  
  return data; 
}

export async function verifySSO(payload) {
  const { data, error } = await supabase.functions.invoke('sso-verify', {
    method: 'POST',
    body: payload, 
  });

  if (error) {
    const status = error.context?.status || 500; 
    return {
      ok: false,
      status: status,
      data: {},
      code: null,
      message: error.message || "Unknown error",
    };
  }

  return {
    ok: true,
    status: 200,
    data: data,
    code: data?.code ?? null,
    message: data?.message ?? "",
  };
}

// --- Payment Functions (UPDATED) ---

/**
 * Creates a payment and simultaneously creates an Order record in the DB.
 * * @param {number} amount - Amount in IQD
 * @param {string} currency - 'IQD'
 * @param {string} description - Payment description
 * @param {string} redirectUri - Callback URL
 * @param {object} orderData - { user_id, items, delivery_type, pick_up_branch, delivery_address }
 */
export async function createPayment(amount, currency = 'IQD', description = '', redirectUri = '', orderData = {}) {
  const { data, error } = await supabase.functions.invoke('fib-create-payment', {
    method: 'POST',
    body: { 
      amount, 
      currency, 
      description, 
      // Spread the order data so it is sent as top-level fields to the Edge Function
      ...orderData 
    },
  });

  if (error) throw new Error(error.message);
  return data; // Returns { paymentId, qrCode, readableCode, personalAppLink, ... }
}

export async function checkPaymentStatus(paymentId) {
  const { data, error } = await supabase.functions.invoke('fib-check-status', {
    method: 'POST',
    body: { paymentId },
  });

  if (error) throw new Error(error.message);
  return data; // Returns { status: 'PAID'|'PENDING'..., paidAt?, ... }
}

export async function cancelPayment(paymentId) {
  const { data, error } = await supabase.functions.invoke('fib-cancel-payment', {
    method: 'POST',
    body: { paymentId },
  });

  if (error) throw new Error(error.message);
  return data; 
}

export async function refundPayment(paymentId) {
  const { data, error } = await supabase.functions.invoke('fib-refund', {
    method: 'POST',
    body: { paymentId },
  });

  if (error) throw new Error(error.message);
  return data; 
}