import React, { useState, useEffect, useRef } from 'react';
import { Trash2, ChevronDown, ChevronUp, MapPin, Truck, CreditCard, User, Loader, Plus, Minus } from 'lucide-react';
import { useFIB } from '../contexts/FIBContext';
import { supabase } from '../supabaseClient';
import { useNavigate } from 'react-router-dom';
// 1. Import Payment Functions
import { createPayment, checkPaymentStatus } from '../services/fibSsoClient';

// --- Reusable Collapsible Card Component (Unchanged) ---
const CollapsibleCard = ({ title, icon: Icon, children, isOpen, onToggle }) => {
  return (
    <div className="border rounded-lg shadow-sm bg-white mb-4 overflow-hidden">
      <button
        onClick={onToggle}
        className="w-full flex items-center justify-between p-4 bg-gray-50 hover:bg-gray-100 transition-colors"
      >
        <div className="flex items-center gap-2">
          {Icon && <Icon className="w-5 h-5 text-gray-600" />}
          <span className="font-bold text-gray-800">{title}</span>
        </div>
        {isOpen ? <ChevronUp className="w-5 h-5 text-gray-500" /> : <ChevronDown className="w-5 h-5 text-gray-500" />}
      </button>
      
      <div
        className={`transition-all duration-300 ease-in-out overflow-hidden ${
          isOpen ? "max-h-[800px] opacity-100" : "max-h-0 opacity-0"
        }`}
      >
        <div className="p-4 border-t">
          {children}
        </div>
      </div>
    </div>
  );
};

// --- Main Cart Page Component ---
export default function CartPage() {
  const { user } = useFIB();
  const navigate = useNavigate();
  const [cartItems, setCartItems] = useState([]);
  const [loading, setLoading] = useState(true);
  const [openSection, setOpenSection] = useState('personal'); 
  const [dbUserId, setDbUserId] = useState(null);
  // Payment State
  const [isPaying, setIsPaying] = useState(false);
  const [paymentStatus, setPaymentStatus] = useState(null); // 'PENDING', 'PAID', etc.
  const [isSavingUser, setIsSavingUser] = useState(false);
  const [formData, setFormData] = useState({
    name: '',
    phone: '',
    orderType: 'pickup',
    branch: 'Erbil',
    address: '',
    deliveryTime: ''
  });
  const [termsAccepted, setTermsAccepted] = useState(false);

  // Ref to track polling interval so we can clear it on unmount
  const pollTimer = useRef(null);

  // --- Fetch Cart Data (Existing Logic) ---
  useEffect(() => {
    const fetchCart = async () => {
      try {
        setLoading(true);

        // 1. Read from Local Storage
        const storedCart = localStorage.getItem('fib_cart');
        const localCart = storedCart ? JSON.parse(storedCart) : [];

        if (localCart.length === 0) {
          setCartItems([]);
          setLoading(false);
          return;
        }

        // 2. Get Product Details from Supabase
        // We still need Supabase to tell us the Price, Name, and Image of the IDs in storage
        const productRefs = localCart.map(item => item.product_id);
        
        const { data: productsData, error: productsError } = await supabase
          .from('products')
          .select('*')
          .in('ref_number', productRefs);

        if (productsError) throw productsError;

        // 3. Merge Local Qty with Database Details
        const mergedItems = productsData.map(product => {
          const cartItem = localCart.find(c => String(c.product_id) === String(product.ref_number));
          const requestedQty = cartItem ? cartItem.qty : 0;

          // Cap quantity to available stock (wh_qty)
          const availableStock = Math.max(0, product.wh_qty || 0);
          const cappedQty = Math.min(requestedQty, availableStock);

          return {
            ...product,
            quantity: cappedQty,
            displayImage: product.image_urls?.thumbnail || product.image_urls?.medium || product.image_urls?.main || "/placeholder.png"
          };
        });

        setCartItems(mergedItems);

        // 4. Update localStorage to reflect capped quantities
        const cappedLocalCart = mergedItems.map(item => ({
          product_id: item.ref_number,
          qty: item.quantity
        }));
        localStorage.setItem('fib_cart', JSON.stringify(cappedLocalCart));

        // 5. Pre-fill Name/Phone if available from SSO (Session)
        // Even though user isn't in DB, we still have their info in memory from the login
        if (user) {
            setFormData(prev => ({
              ...prev,
              name: user.displayName || user.name || prev.name,
              phone: user.phone || prev.phone
            }));
        }

      } catch (error) {
        console.error("Error processing cart:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchCart();
    
    return () => {
        if (pollTimer.current) clearInterval(pollTimer.current);
    };
  }, [user]); // Keep user dependency just in case SSO loads late

  // --- Sync Cart Changes (Existing Logic) ---
  // const updateDbCart = async (newItems) => {
  //   if (!user || !user.name) return;
  //   const simpleCart = newItems.map(item => ({
  //     product_id: item.ref_number, 
  //     qty: item.quantity
  //   }));
  //   try {
  //     const { data: users } = await supabase
  //       .from('fibUsers')
  //       .select('id')
  //       .eq('name', user.name)
  //       .limit(1);
      
  //     if (users && users.length > 0) {
  //       await supabase.from('fibUsers').update({ cart: simpleCart }).eq('id', users[0].id);
  //     }
  //   } catch (err) {
  //     console.error("Error syncing cart to DB:", err);
  //   }
  // };
  const updateLocalCart = (newItems) => {
    // Convert full product objects back to simple { id, qty } for storage
    const simpleCart = newItems.map(item => ({
      product_id: item.ref_number, 
      qty: item.quantity
    }));
    
    localStorage.setItem('fib_cart', JSON.stringify(simpleCart));
    
    // Note: We no longer sync to Supabase 'fibUsers' table here
  };
  const handleSavePersonalDetails = async () => {
    // 1. Validation
    if (!formData.name || !formData.name.trim()) {
      alert("Please enter your full name.");
      return;
    }
    if (!formData.phone || !formData.phone.trim()) {
      alert("Please enter a valid phone number.");
      return;
    }

    setIsSavingUser(true);

    try {
      // 2. Check if this phone number already exists in DB
      // We use phone as the unique key here since we don't have a guaranteed DB ID from login anymore
      const { data: existingUser, error: searchError } = await supabase
        .from('fibUsers')
        .select('id')
        .eq('phone_number', formData.phone)
        .single();

      if (searchError && searchError.code !== 'PGRST116') { // PGRST116 is "not found"
        throw searchError;
      }

      let dbId = existingUser?.id;

      if (dbId) {
        // A. UPDATE existing user
        const { error: updateError } = await supabase
          .from('fibUsers')
          .update({
            name: formData.name, // Update name in case they corrected it
            updated_at: new Date().toISOString()
          })
          .eq('id', dbId);

        if (updateError) throw updateError;

      } else {
        // B. INSERT new user (The logic we removed from login)
        const { data: newUser, error: insertError } = await supabase
          .from('fibUsers')
          .insert({
            name: formData.name,
            phone_number: formData.phone,
            created_at: new Date().toISOString()
          })
          .select('id')
          .single();

        if (insertError) throw insertError;
        dbId = newUser.id;
      }
      setDbUserId(dbId);

      // 3. Success! Move to next section
      setOpenSection('order');

    } catch (err) {
      console.error("Failed to save user details:", err);
      alert("We encountered an error saving your details. Please try again.");
    } finally {
      setIsSavingUser(false);
    }
  };

  // Helper function to get warehouse quantity (total stock)
  const getTotalStock = (item) => {
    return Math.max(0, item.wh_qty || 0); // Use wh_qty as it represents total stock
  };

  const handleQuantityChange = (ref_number, change) => {
    const updatedCart = cartItems.map(item => {
      if (item.ref_number === ref_number) {
        const totalStock = getTotalStock(item);
        const newQty = Math.max(1, Math.min(totalStock, item.quantity + change));
        return { ...item, quantity: newQty };
      }
      return item;
    });
    setCartItems(updatedCart);
    updateLocalCart(updatedCart);
  };

  const handleRemoveItem = (ref_number) => {
    const updatedCart = cartItems.filter(item => item.ref_number !== ref_number);
    setCartItems(updatedCart);
    updateLocalCart(updatedCart);
  };

  // --- NEW: Payment Logic ---

  const clearUserCart = async () => {
    // Empties the cart in Supabase after successful payment
    try {
      const { data: users } = await supabase.from('fibUsers').select('id').eq('name', user.name).limit(1);
      if (users && users.length > 0) {
        await supabase.from('fibUsers').update({ cart: [] }).eq('id', users[0].id);
      }
    } catch (err) {
      console.error("Failed to clear cart", err);
    }
  };

  const handlePayWithFIB = async () => {
    // 1. Validation
    if (!dbUserId) {
      alert("Please confirm your personal details first.");
      setOpenSection('personal');
      return;
    }
    if (formData.orderType === 'delivery' && !formData.address) {
      alert("Please enter a delivery address.");
      setOpenSection('order');
      return;
    }
    if (!termsAccepted) {
      alert(`Please accept the ${formData.orderType === 'delivery' ? 'delivery' : 'pickup'} terms to proceed.`);
      setOpenSection('order');
      return;
    }

    setIsPaying(true);
    setPaymentStatus("INITIALIZING");

    try {
      const totalAmount = calculateTotal();
      if (totalAmount < 250) {
        alert("Minimum order amount is 250 IQD");
        setIsPaying(false);
        return;
      }

      let description = `Order by ${formData.name} (${formData.phone})`;
      if (description.length > 50) {
          description = description.substring(0, 47) + "...";
      }
      // --- FIX IS HERE ---
      const paymentResponse = await createPayment(
        totalAmount, 
        'IQD', 
        description,
        "",
        { 
          user_id: dbUserId,
          items: cartItems, 
          delivery_type: formData.orderType === 'pickup' ? 'PICKUP' : 'DELIVERY',
          pick_up_branch: formData.branch,
          delivery_address: formData.address
        }
      );

      if (!paymentResponse || !paymentResponse.paymentId) {
        throw new Error("Invalid response from payment server");
      }

      const { paymentId, personalAppLink } = paymentResponse;

      // 3. Trigger Native Payment Sheet
      if (personalAppLink) {
        window.location.href = personalAppLink;
      } else {
        throw new Error("No Personal App Link received");
      }

      // 4. Start Polling
      setPaymentStatus("WAITING_FOR_PAYMENT");
      
      pollTimer.current = setInterval(async () => {
        try {
          const statusData = await checkPaymentStatus(paymentId);
          console.log("Polling status:", statusData.status);

          if (statusData.status === 'PAID') {
            clearInterval(pollTimer.current);
            setPaymentStatus("SUCCESS");
            
            setCartItems([]);
            await clearUserCart();
            
            // Navigate internally using React Router
            setTimeout(() => {
               navigate('/orders'); 
            }, 1500);
          } else if (statusData.status === 'DECLINED') {
             clearInterval(pollTimer.current);
             setPaymentStatus("DECLINED");
             alert("Payment was declined.");
             setIsPaying(false);
          }
        } catch (pollError) {
          console.warn("Polling error", pollError);
        }
      }, 6000); 

    } catch (error) {
      console.error("Payment Error:", error);
      alert(`Payment Failed: ${error.message}`);
      setIsPaying(false);
      setPaymentStatus(null);
    }
  };

  // Helper
  const toggleSection = (section) => {
    setOpenSection(openSection === section ? null : section);
  };
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));

    // Reset terms acceptance when order type changes
    if (name === 'orderType') {
      setTermsAccepted(false);
    }
  };
  const formatPrice = (p) => p ? `${Number(p).toLocaleString()} IQD` : "No Price";
  const calculateTotal = () => cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);

  // Loading View
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-gray-50">
        <div className="text-center">
          <Loader className="w-10 h-10 animate-spin text-blue-600 mx-auto mb-4" />
          <p className="text-gray-500">Loading your cart...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8 px-4 md:px-8">
      <div className="max-w-7xl mx-auto">
        <h1 className="text-3xl font-extrabold mb-8 text-gray-900"></h1>

        {cartItems.length === 0 && !paymentStatus === 'SUCCESS' ? (
          <div className="text-center py-20 bg-white rounded-lg border shadow-sm">
            <p className="text-gray-500 text-xl">Your cart is empty.</p>
            <p className="text-gray-400 mt-2">Go explore our collection!</p>
          </div>
        ) : (
          <div className="flex flex-col lg:flex-row gap-8">
            
            {/* --- LEFT COLUMN --- */}
            <div className="w-full lg:w-2/3 space-y-4">
              {cartItems.map((item) => (
                <div key={item.ref_number} className="bg-white border rounded-lg p-4 shadow-sm flex flex-row items-center gap-4">
                  <div className="w-24 h-24 md:w-32 md:h-32 flex-shrink-0 bg-gray-100 rounded-md overflow-hidden border">
                    <img src={item.displayImage} alt={item.brand} className="w-full h-full object-contain p-2" />
                  </div>
                  <div className="flex-grow min-w-0">
                    <h3 className="text-lg font-bold text-gray-800 truncate">{item.brand}</h3>
                    <p className="text-sm text-gray-500 font-mono">{item.ref_number}</p>
                    <div className="mt-1 text-blue-600 font-semibold">{formatPrice(item.discounted_price || item.price)}</div>
                  </div>
                  {/* Quantity Controls with Stock Validation */}
                  <div className="flex flex-col items-end gap-2">
                    {!isPaying && (() => {
                      const totalStock = getTotalStock(item);
                      const isOutOfStock = totalStock === 0;
                      const isAtMax = item.quantity >= totalStock;
                      const isAtMin = item.quantity <= 1;

                      return (
                        <div className="flex flex-col items-end gap-1">
                          {/* Stock Warning for Out of Stock */}
                          {isOutOfStock && (
                            <span className="text-xs text-red-600 font-bold bg-red-50 px-2 py-1 rounded">
                              OUT OF STOCK
                            </span>
                          )}

                          {/* Quantity Buttons */}
                          <div className="flex items-center gap-2">
                            <button
                              onClick={() => handleQuantityChange(item.ref_number, -1)}
                              disabled={isAtMin || isOutOfStock}
                              className={`
                                w-8 h-8 rounded-md flex items-center justify-center transition-all
                                ${isAtMin || isOutOfStock
                                  ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                                  : 'bg-gray-100 text-gray-700 hover:bg-blue-50 hover:text-blue-600 active:scale-95'
                                }
                              `}
                              aria-label="Decrease quantity"
                            >
                              <Minus className="w-4 h-4" />
                            </button>

                            <span className="font-bold text-gray-800 min-w-[2rem] text-center">
                              {item.quantity}
                            </span>

                            <button
                              onClick={() => handleQuantityChange(item.ref_number, 1)}
                              disabled={isAtMax || isOutOfStock}
                              className={`
                                w-8 h-8 rounded-md flex items-center justify-center transition-all
                                ${isAtMax || isOutOfStock
                                  ? 'bg-gray-200 text-gray-400 cursor-not-allowed'
                                  : 'bg-gray-100 text-gray-700 hover:bg-blue-50 hover:text-blue-600 active:scale-95'
                                }
                              `}
                              aria-label="Increase quantity"
                            >
                              <Plus className="w-4 h-4" />
                            </button>
                          </div>

                          {/* Stock Information */}
                          {!isOutOfStock && totalStock < 10 && (
                            <span className={`text-xs ${isAtMax ? 'text-red-600 font-semibold' : 'text-gray-500'}`}>
                              {isAtMax ? 'Maximum reached' : `Max: ${totalStock} available`}
                            </span>
                          )}

                          {isOutOfStock && (
                            <span className="text-xs text-red-600 text-right">
                              Please remove this item
                            </span>
                          )}
                        </div>
                      );
                    })()}

                    {/* Remove Button */}
                    {!isPaying && (
                      <button
                        onClick={() => handleRemoveItem(item.ref_number)}
                        className="text-red-500 text-sm flex items-center gap-1 hover:text-red-600 transition-colors"
                      >
                        <Trash2 className="w-4 h-4" /> Remove
                      </button>
                    )}
                  </div>
                </div>
              ))}
              <div className="bg-white p-4 rounded-lg border shadow-sm flex justify-between items-center mt-4">
                <span className="text-lg font-medium text-gray-600">Total Amount:</span>
                <span className="text-2xl font-bold text-gray-900">{formatPrice(calculateTotal())}</span>
              </div>
            </div>

            {/* --- RIGHT COLUMN --- */}
            <div className="w-full lg:w-1/3">
              {/* Personal & Order Cards (Same as before) */}
              <CollapsibleCard 
                title="Personal Details" 
                icon={User} 
                isOpen={openSection === 'personal'} 
                onToggle={() => toggleSection('personal')}
              >
                  <div className="space-y-4">
                    <div>
                      <label className="block text-xs font-semibold text-gray-500 mb-1">FULL NAME</label>
                      <input 
                        type="text" 
                        name="name" 
                        value={formData.name} 
                        onChange={handleInputChange} 
                        placeholder="Full Name" 
                        className="w-full p-2 border rounded focus:ring-2 focus:ring-blue-500 focus:outline-none" 
                      />
                    </div>
                    
                    <div>
                      <label className="block text-xs font-semibold text-gray-500 mb-1">PHONE NUMBER</label>
                      <input 
                        type="tel" 
                        name="phone" 
                        value={formData.phone} 
                        onChange={handleInputChange} 
                        placeholder="0750xxxxxxx" 
                        className="w-full p-2 border rounded focus:ring-2 focus:ring-blue-500 focus:outline-none" 
                      />
                    </div>

                    {/* --- PRIVACY DISCLAIMER --- */}
                    <div className="bg-blue-50 p-3 rounded-md border border-blue-100">
                      <p className="text-xs text-blue-800 leading-relaxed">
                        <span className="font-bold">Privacy Notice:</span> By proceeding, you agree that we will store your name and phone number to process your order and contact you regarding delivery.
                      </p>
                    </div>

                    <button 
                      onClick={handleSavePersonalDetails} 
                      disabled={isSavingUser}
                      className="w-full bg-gray-800 hover:bg-gray-900 text-white py-2 rounded transition-colors flex justify-center items-center gap-2"
                    >
                      {isSavingUser ? (
                        <>
                          <Loader className="w-4 h-4 animate-spin" /> Saving...
                        </>
                      ) : (
                        "Confirm & Next"
                      )}
                    </button>
                  </div>
              </CollapsibleCard>

              <CollapsibleCard title="Order Details" icon={Truck} isOpen={openSection === 'order'} onToggle={() => toggleSection('order')}>
                  {/* Inputs... (Same as previous file) */}
                  <div className="space-y-4">
                      <div className="flex gap-4 p-1 bg-gray-100 rounded-lg">
                    <button
                      onClick={() => {
                        setFormData({...formData, orderType: 'pickup'});
                        setTermsAccepted(false);
                      }}
                      className={`flex-1 py-2 rounded-md text-sm font-medium transition-all ${
                        formData.orderType === 'pickup'
                          ? 'bg-white shadow-sm text-blue-600'
                          : 'text-gray-500 hover:text-gray-700'
                      }`}
                    >
                      Pickup
                    </button>
                    <button
                      onClick={() => {
                        setFormData({...formData, orderType: 'delivery'});
                        setTermsAccepted(false);
                      }}
                      className={`flex-1 py-2 rounded-md text-sm font-medium transition-all ${
                        formData.orderType === 'delivery'
                          ? 'bg-white shadow-sm text-blue-600'
                          : 'text-gray-500 hover:text-gray-700'
                      }`}
                    >
                      Delivery
                    </button>
                  </div>

                  {/* Conditional Content */}
                  {formData.orderType === 'pickup' ? (
                    <div className="animate-fadeIn">
                      <label className="block text-sm font-medium text-gray-700 mb-2">Select Branch</label>
                      <div className="space-y-2">
                        <label className="flex items-center p-3 border rounded-lg cursor-pointer hover:bg-gray-50">
                          <input 
                            type="radio" 
                            name="branch" 
                            value="Erbil"
                            checked={formData.branch === 'Erbil'}
                            onChange={handleInputChange}
                            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300"
                          />
                          <span className="ml-3 block text-sm font-medium text-gray-700">Erbil Branch</span>
                        </label>
                        <label className="flex items-center p-3 border rounded-lg cursor-pointer hover:bg-gray-50">
                          <input 
                            type="radio" 
                            name="branch" 
                            value="Mosul"
                            checked={formData.branch === 'Mosul'}
                            onChange={handleInputChange}
                            className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300"
                          />
                          <span className="ml-3 block text-sm font-medium text-gray-700">Mosul Branch</span>
                        </label>
                      </div>
                    </div>
                  ) : (
                    <div className="animate-fadeIn space-y-3">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center gap-1">
                          <MapPin className="w-3 h-3"/> Address
                        </label>
                        <textarea 
                          name="address"
                          value={formData.address}
                          onChange={handleInputChange}
                          rows="2"
                          placeholder="Full address details..."
                          className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none"
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">Preferred Time</label>
                        <input 
                          type="text" 
                          name="deliveryTime"
                          value={formData.deliveryTime}
                          onChange={handleInputChange}
                          placeholder="e.g., 2:00 PM - 5:00 PM"
                          className="w-full p-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none"
                        />
                      </div>
                    </div>
                  )}

                  {/* Terms and Conditions Checkbox */}
                  <div className="mt-4 bg-gray-50 p-4 rounded-lg border border-gray-200">
                    {formData.orderType === 'delivery' ? (
                      <div className="space-y-3">
                        <div className="text-sm text-gray-700 leading-relaxed space-y-2">
                          <p className="font-semibold text-gray-800">Please note:</p>
                          <ul className="list-disc list-inside space-y-1 text-xs">
                            <li>Delivery time can range between 1-2 days or more in some cases</li>
                            <li>Delivery time depends on: branch stock availability, delivery address, preferred delivery time, and other factors</li>
                            <li>For specific delivery time estimates, please contact customer support before ordering</li>
                            <li>Preferred delivery time may not be followed exactly and should be between 2:00 PM and 9:00 PM</li>
                          </ul>
                        </div>
                        <label className="flex items-start gap-2 cursor-pointer group">
                          <input
                            type="checkbox"
                            checked={termsAccepted}
                            onChange={(e) => setTermsAccepted(e.target.checked)}
                            className="mt-1 h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded cursor-pointer"
                          />
                          <span className="text-sm text-gray-700 group-hover:text-gray-900">
                            I understand and accept the delivery terms
                          </span>
                        </label>
                      </div>
                    ) : (
                      <div className="space-y-3">
                        <div className="text-sm text-gray-700 leading-relaxed space-y-2">
                          <p className="font-semibold text-gray-800">Please note:</p>
                          <ul className="list-disc list-inside space-y-1 text-xs">
                            <li>Some items are only available at one branch, not all branches</li>
                            <li>Please contact customer support to confirm the item you picked is available at your selected branch</li>
                            <li>We can transfer watches between branches if needed for your pickup convenience</li>
                          </ul>
                        </div>
                        <label className="flex items-start gap-2 cursor-pointer group">
                          <input
                            type="checkbox"
                            checked={termsAccepted}
                            onChange={(e) => setTermsAccepted(e.target.checked)}
                            className="mt-1 h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded cursor-pointer"
                          />
                          <span className="text-sm text-gray-700 group-hover:text-gray-900">
                            I understand and accept the pickup terms
                          </span>
                        </label>
                      </div>
                    )}
                  </div>

                      <button 
                        onClick={() => {
                          if (!termsAccepted) {
                            alert("Please accept the terms to proceed to payment.");
                            return;
                          }
                          setOpenSection('payment');
                        }} 
                        className="w-full bg-gray-800 text-white py-2 rounded"
                      >
                        Next
                      </button>
                  </div>
              </CollapsibleCard>

              {/* PAYMENT CARD - MODIFIED */}
              <CollapsibleCard title="Payment" icon={CreditCard} isOpen={openSection === 'payment'} onToggle={() => toggleSection('payment')}>
                <div className="space-y-4 text-center">
                  <p className="text-sm text-gray-500">
                    Total to pay: <span className="font-bold text-gray-900">{formatPrice(calculateTotal())}</span>
                  </p>
                  
                  {isPaying ? (
                    <div className="bg-blue-50 p-4 rounded-lg border border-blue-100">
                      <Loader className="w-8 h-8 animate-spin text-blue-600 mx-auto mb-2" />
                      <p className="font-semibold text-blue-800">
                        {paymentStatus === 'WAITING_FOR_PAYMENT' ? "Processing Payment..." : "Initializing..."}
                      </p>
                      <p className="text-xs text-blue-600 mt-1">
                        Please complete the payment in the FIB sheet.
                      </p>
                    </div>
                  ) : (
                    <button 
                      onClick={handlePayWithFIB}
                      className="w-full bg-[#08B0A0] hover:bg-[#068f82] text-white font-['SF_Pro'] font-black py-3 px-4 rounded-lg shadow-md transition duration-300 text-center"
                    >
                      Pay
                    </button>
                  )}
                  
                  <p className="text-xs text-gray-400 mt-2">Secure payment powered by First Iraqi Bank</p>
                </div>
              </CollapsibleCard>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}