import React, { useState, useEffect, useRef } from 'react';
import { Link, useLocation } from 'react-router-dom';

/**
 * HeroSection - Full-screen video carousel with parallax effect
 * Features:
 * - Auto-playing video carousel
 * - Parallax scrolling effect
 * - Bottom-left positioned text with description
 * - Call-to-action button
 */

// Hero slides configuration - easily editable
const heroSlides = [
  {
    type: 'video',
    src: '/videos/hero-1.mp4',
    poster: '/Icons and Logos/HomePageMaterials/Landing Images/2019LGC-1600x800-PR.jpg',
    title: 'Timeless Elegance',
    description: 'Discover our curated collection of luxury timepieces from the world\'s finest watchmakers.',
    buttonText: 'Explore Collection',
    buttonLink: '/store',
  },
  {
    type: 'video',
    src: '/videos/hero-2.mp4',
    poster: '/Icons and Logos/HomePageMaterials/Landing Images/casio-mens-outdoor-watch-prt-b50-main.jpg',
    title: 'Precision & Craft',
    description: 'Every second counts. Experience the artistry of Swiss engineering.',
    buttonText: 'Shop Now',
    buttonLink: '/store',
  },
  {
    type: 'image',
    src: '/Icons and Logos/HomePageMaterials/Landing Images/WBE5116_FC8266_1000.jpg_impolicy=th-pdp-day-night-day.jpg',
    title: 'New Arrivals',
    description: 'Be the first to own the latest additions to our exclusive collection.',
    buttonText: 'View New Arrivals',
    buttonLink: '/store',
  },
];

const HeroSection = () => {
  const [currentSlide, setCurrentSlide] = useState(0);
  const [scrollY, setScrollY] = useState(0);
  const sectionRef = useRef(null);
  const location = useLocation();
  const isFibMode = location.pathname.startsWith('/fib');
  const basePath = isFibMode ? '/fib' : '';

  // Parallax scroll effect
  useEffect(() => {
    const handleScroll = () => {
      if (sectionRef.current) {
        const rect = sectionRef.current.getBoundingClientRect();
        if (rect.top <= 0 && rect.bottom >= 0) {
          setScrollY(Math.abs(rect.top) * 0.5);
        }
      }
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Auto-advance carousel
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % heroSlides.length);
    }, 6000);
    return () => clearInterval(interval);
  }, []);

  const goToSlide = (index) => {
    setCurrentSlide(index);
  };

  const currentContent = heroSlides[currentSlide];

  return (
    <section
      ref={sectionRef}
      className="relative h-screen w-full overflow-hidden bg-black"
    >
      {/* Background Media with Parallax */}
      <div
        className="absolute inset-0 w-full h-[120%]"
        style={{ transform: `translateY(${scrollY}px)` }}
      >
        {heroSlides.map((slide, index) => (
          <div
            key={index}
            className={`absolute inset-0 transition-opacity duration-1000 ease-in-out ${
              index === currentSlide ? 'opacity-100' : 'opacity-0'
            }`}
          >
            {slide.type === 'video' ? (
              <video
                autoPlay
                muted
                loop
                playsInline
                poster={slide.poster}
                className="w-full h-full object-cover"
              >
                <source src={slide.src} type="video/mp4" />
                {/* Fallback to poster image */}
                <img src={slide.poster} alt={slide.title} className="w-full h-full object-cover" />
              </video>
            ) : (
              <img
                src={slide.src}
                alt={slide.title}
                className="w-full h-full object-cover"
              />
            )}
          </div>
        ))}

        {/* Gradient Overlay */}
        <div className="absolute inset-0 bg-gradient-to-t from-black via-black/40 to-transparent" />
        <div className="absolute inset-0 bg-gradient-to-r from-black/60 via-transparent to-transparent" />
      </div>

      {/* Content - Bottom Left */}
      <div className="absolute bottom-0 left-0 right-0 p-8 md:p-16 lg:p-24 z-10">
        <div className="max-w-2xl">
          {/* Animated Title */}
          <h1
            key={`title-${currentSlide}`}
            className="text-4xl md:text-6xl lg:text-7xl font-light text-white mb-4 tracking-tight animate-fadeInUp"
            style={{ fontFamily: "'Cormorant Garamond', serif" }}
          >
            {currentContent.title}
          </h1>

          {/* Description */}
          <p
            key={`desc-${currentSlide}`}
            className="text-lg md:text-xl text-white/80 mb-8 max-w-lg leading-relaxed animate-fadeInUp animation-delay-200"
          >
            {currentContent.description}
          </p>

          {/* CTA Button */}
          <Link
            to={`${basePath}${currentContent.buttonLink}`}
            className="group inline-flex items-center gap-3 bg-white text-black px-8 py-4 text-sm font-medium tracking-widest uppercase transition-all duration-300 hover:bg-amber-400 hover:text-black animate-fadeInUp animation-delay-400"
          >
            {currentContent.buttonText}
            <svg
              className="w-4 h-4 transform transition-transform group-hover:translate-x-2"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
            </svg>
          </Link>
        </div>

        {/* Slide Indicators */}
        <div className="flex gap-3 mt-12">
          {heroSlides.map((_, index) => (
            <button
              key={index}
              onClick={() => goToSlide(index)}
              className={`h-1 transition-all duration-300 ${
                index === currentSlide
                  ? 'w-12 bg-amber-400'
                  : 'w-6 bg-white/40 hover:bg-white/60'
              }`}
              aria-label={`Go to slide ${index + 1}`}
            />
          ))}
        </div>
      </div>

      {/* Scroll Indicator */}
      <div className="absolute bottom-8 right-8 md:right-16 lg:right-24 z-10 flex flex-col items-center gap-2 text-white/60">
        <span className="text-xs tracking-widest uppercase rotate-90 origin-center translate-x-4">Scroll</span>
        <div className="w-px h-16 bg-gradient-to-b from-white/60 to-transparent animate-pulse" />
      </div>
    </section>
  );
};

export default HeroSection;


