import React, { useEffect, useState } from 'react';
import { 
  Package, 
  Clock, 
  CheckCircle, 
  XCircle, 
  ChevronRight, 
  ChevronDown, 
  ExternalLink, 
  Loader, 
  AlertCircle,
  MapPin,
  QrCode,
  Trash2,      // New Icon
  RotateCcw,   // New Icon
  CreditCard,
  ShoppingBag, 
  ArrowRight   // New Icon
} from 'lucide-react';
import { useFIB } from '../contexts/FIBContext'; 
import { supabase } from '../supabaseClient';
// Add these to your existing imports
import { cancelPayment, refundPayment, checkPaymentStatus } from '../services/fibSsoClient'; // adjust path to where your api file is

export default function OrdersFIBPage() {
  const { user } = useFIB();
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(true);
  
  // Track which specific order is currently performing an action (cancelling/refunding)
  const [actionLoading, setActionLoading] = useState(null); 
  const [expandedOrderId, setExpandedOrderId] = useState(null);

  useEffect(() => {
    if (!user || !user.id) {
      setLoading(false);
      return;
    }
    fetchOrders();
  }, [user]);

  const pollForStatus = async (fibPaymentId, targetStatuses, maxAttempts = 12) => {
    let attempts = 0;

    while (attempts < maxAttempts) {
      // 1. Wait 10 seconds (10000 ms)
      await new Promise(resolve => setTimeout(resolve, 10000));
      
      attempts++;
      console.log(`Polling attempt ${attempts}/${maxAttempts} for ${fibPaymentId}...`);

      // 2. Call your existing checkPaymentStatus function
      // This calls the Edge Function -> Calls FIB -> Updates DB if changed
      const result = await checkPaymentStatus(fibPaymentId);
      const currentStatus = result.status?.toUpperCase();

      // 3. Check if status matches one of our targets
      if (targetStatuses.includes(currentStatus)) {
        return true; // Success!
      }
    }

    return false; // Timed out
  };

  const fetchOrders = async () => {
    try {
      const { data, error } = await supabase
        .from('ordersFIB')
        .select(`
          *,
          transactionFIB (
            id,
            fib_payment_id,
            amount,
            currency,
            status,
            personal_app_link,
            readable_code,
            qr_code
          )
        `)
        .eq('user_id', user.id) 
        .order('order_date', { ascending: false });

      if (error) throw error;
      setOrders(data || []);

    } catch (err) {
      console.error("Error fetching orders:", err);
    } finally {
      setLoading(false);
    }
  };

  // --- Logic Helpers ---

  const toggleDetails = (orderId) => {
    setExpandedOrderId(expandedOrderId === orderId ? null : orderId);
  };

  // Check if order is eligible for refund (Paid + within 7 days)
  const isRefundEligible = (orderDate, status) => {
    const s = status ? status.toUpperCase() : '';
    if (s !== 'PAID' && s !== 'CONFIRMED') return false;

    const created = new Date(orderDate);
    const now = new Date();
    const diffTime = Math.abs(now - created);
    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 

    return diffDays <= 7;
  };

  // --- Actions ---

  const handleCancelOrder = async (orderId) => {
    if (!window.confirm("Are you sure you want to cancel this order?")) return;
    
    setActionLoading(orderId); // Start loading spinner
    try {
      const order = orders.find(o => o.id === orderId);
      const fibPaymentId = order?.transactionFIB?.fib_payment_id;
      if (!fibPaymentId) throw new Error("No payment ID found");

      // 1. Request Cancellation
      await cancelPayment(fibPaymentId);
      
      // 2. Start Polling (Wait for 'DECLINED' or 'CANCELLED')
      // Note: FIB usually returns 'DECLINED' for cancelled payments
      const success = await pollForStatus(fibPaymentId, ['DECLINED', 'CANCELLED']);

      if (success) {
        await fetchOrders(); // Refresh UI with new data from DB
        alert("Order cancelled successfully.");
      } else {
        await fetchOrders(); // Refresh anyway just in case
        alert("Cancellation request sent, but status update is delayed. Please check back later.");
      }

    } catch (err) {
      console.error("Cancel error:", err);
      alert(`Failed to cancel: ${err.message}`);
    } finally {
      setActionLoading(null); // Stop loading spinner
    }
  };

  const handleRefundRequest = async (orderId) => {
    if (!window.confirm("Request a refund? This cannot be undone.")) return;

    setActionLoading(orderId);
    try {
      const order = orders.find(o => o.id === orderId);
      const fibPaymentId = order?.transactionFIB?.fib_payment_id;
      if (!fibPaymentId) throw new Error("No payment ID found");

      // 1. Request Refund
      await refundPayment(fibPaymentId);

      // 2. Start Polling (Wait for 'REFUNDED' or 'REFUND_REQUESTED')
      const success = await pollForStatus(fibPaymentId, ['REFUNDED', 'REFUND_REQUESTED'],30);

      if (success) {
        await fetchOrders();
        alert("Refund processed successfully.");
      } else {
        // Even if polling times out, we update the UI because the initial 
        // request likely succeeded (202 Accepted)
        await fetchOrders(); 
        alert("Refund request submitted. Status will update shortly.");
      }

    } catch (err) {
      console.error("Refund error:", err);
      alert(`Refund failed: ${err.message}`);
    } finally {
      setActionLoading(null);
    }
  };

  const handleRefreshStatus = async (orderId) => {
    setActionLoading(orderId);
    try {
      const order = orders.find(o => o.id === orderId);
      const fibPaymentId = order?.transactionFIB?.fib_payment_id;
      if (fibPaymentId) {
        await checkPaymentStatus(fibPaymentId); // Force API check
        await fetchOrders(); // Refresh UI
      }
    } catch (e) {
      console.error(e);
    } finally {
      setActionLoading(null);
    }
  };

  // --- Render Helpers ---

  const getStatusConfig = (status) => {
    const s = status ? status.toUpperCase() : 'UNKNOWN';
    switch (s) {
      case 'PAID':
      case 'CONFIRMED':
        return { color: 'bg-emerald-50 text-emerald-700 border-emerald-100', icon: CheckCircle, label: 'Paid' };
      case 'PENDING':
      case 'UNPAID':
      case 'PROCESSING':
        return { color: 'bg-amber-50 text-amber-700 border-amber-100', icon: Clock, label: 'Pending Payment' };
      case 'DECLINED':
      case 'CANCELLED':
        return { color: 'bg-rose-50 text-rose-700 border-rose-100', icon: XCircle, label: 'Cancelled' };
      case 'REFUNDED':
        return { color: 'bg-purple-50 text-purple-700 border-purple-100', icon: RotateCcw, label: 'Refunded' };
      case 'REFUND_REQUESTED':
      case 'REFUND_IN_PROGRESS':
        return { color: 'bg-blue-50 text-blue-700 border-blue-100', icon: Loader, label: 'Refund Processing' };
      default:
        return { color: 'bg-gray-50 text-gray-600 border-gray-100', icon: AlertCircle, label: s };
    }
  };
  const formatPrice = (amount) => Number(amount).toLocaleString() + ' IQD';
  
  const formatDate = (dateString) => {
    if (!dateString) return '';
    return new Date(dateString).toLocaleDateString('en-GB', {
      day: 'numeric', month: 'short', year: 'numeric'
    });
  };

  if (loading) return <div className="min-h-screen flex items-center justify-center bg-gray-50"><Loader className="animate-spin text-gray-400" /></div>;

  return (
    <div className="min-h-screen bg-gray-50/50 py-12 px-4 sm:px-6 lg:px-8 font-sans">
      <div className="max-w-4xl mx-auto">
        {orders.length === 0 ? (
          <div className="text-center py-24 bg-white rounded-2xl border border-dashed border-gray-300">
            <div className="bg-gray-50 w-16 h-16 rounded-full flex items-center justify-center mx-auto mb-4">
               <ShoppingBag className="w-8 h-8 text-gray-400" />
            </div>
            <h3 className="text-gray-900 font-semibold text-lg">No orders yet</h3>
            <p className="text-gray-500 mt-1">Your purchase history will appear here.</p>
          </div>
        ) : (
          <div className="space-y-6">
            {orders.map((order) => {
              const tx = order.transactionFIB || {};
              const currentStatus = tx.status || order.order_status || 'PENDING';
              const statusConfig = getStatusConfig(currentStatus);
              const StatusIcon = statusConfig.icon;
              const isPending = ['PENDING', 'UNPAID', 'PROCESSING'].includes(currentStatus?.toUpperCase());
              
              // Refund Logic (Paid + < 12 hours)
              const orderDate = new Date(order.order_date);
              const diffHours = (Date.now() - orderDate.getTime()) / (1000 * 60 * 60);

              const canRefund =
                (currentStatus === 'PAID' || currentStatus === 'CONFIRMED') &&
                diffHours <= 12;
              
              const isExpanded = expandedOrderId === order.id;
              const isActionProcessing = actionLoading === order.id;

              // Items Parsing
              let orderItems = [];
              try {
                  if (Array.isArray(order.items)) orderItems = order.items;
                  else if (typeof order.items === 'string') orderItems = JSON.parse(order.items);
              } catch(e) {}

              // Address Parsing
              let displayAddress = "No address provided";
              if (order.delivery_type === 'DELIVERY') {
                  if (typeof order.delivery_address === 'string') displayAddress = order.delivery_address;
                  else if (order.delivery_address && typeof order.delivery_address === 'object') {
                      displayAddress = Object.values(order.delivery_address).filter(Boolean).join(', ');
                  }
              }

              return (
                <div 
                  key={order.id} 
                  className="bg-white rounded-2xl border border-gray-100 shadow-sm hover:shadow-md transition-all duration-300 overflow-hidden group"
                >
                  
                  {/* === CARD TOP: Main Info === */}
                  <div className="p-6 sm:p-7">
                    <div className="flex flex-col md:flex-row md:items-start justify-between gap-6">
                      
                      {/* Left: ID & Context */}
                      <div className="flex-1">
                        <div className="flex items-center gap-3 mb-3">
                           {/* STATUS PILL */}
                           <div className={`pl-2 pr-3 py-1 rounded-full text-xs font-bold border flex items-center gap-2 ${statusConfig.color}`}>
                              <StatusIcon className={`w-3.5 h-3.5 ${statusConfig.label === 'Refund Processing' ? 'animate-spin' : ''}`} />
                              {statusConfig.label}
                           </div>
                           
                           {/* REFRESH BUTTON */}
                           <button 
                             onClick={(e) => { e.stopPropagation(); handleRefreshStatus(order.id); }}
                             disabled={isActionProcessing}
                             className="p-1.5 rounded-full text-gray-400 hover:text-blue-600 hover:bg-blue-50 transition-colors"
                             title="Refresh Status"
                           >
                             <RotateCcw className={`w-3.5 h-3.5 ${isActionProcessing ? 'animate-spin text-blue-600' : ''}`} />
                           </button>
                        </div>

                        {/* ORDER ID (LARGE) */}
                        <div className="flex items-baseline gap-2">
                           <span className="text-gray-400 font-medium text-lg">#</span>
                           <h2 className="text-2xl sm:text-3xl font-black text-gray-900 tracking-tight font-mono">
                             {order.id.slice(0, 8).toUpperCase()}
                           </h2>
                        </div>
                        <p className="text-sm text-gray-500 mt-1 flex items-center gap-2">
                          <Clock className="w-4 h-4 text-gray-400" /> 
                          Placed on {formatDate(order.order_date)}
                        </p>
                      </div>

                      {/* Right: Price & Previews */}
                      <div className="flex flex-col items-start md:items-end gap-4">
                         <div className="text-left md:text-right">
                            <span className="block text-xs font-semibold text-gray-400 uppercase tracking-wider mb-1">Total Amount</span>
                            <span className="text-2xl sm:text-3xl font-bold text-gray-900">
                               {formatPrice(order.price || order.discounted_price)}
                            </span>
                         </div>
                         
                         {/* Item Stack Preview */}
                         {orderItems.length > 0 && (
                           <div className="flex items-center -space-x-3 hover:space-x-1 transition-all duration-300">
                              {orderItems.slice(0, 4).map((item, i) => (
                                <div key={i} className="w-12 h-12 rounded-xl border-2 border-white bg-gray-50 shadow-sm overflow-hidden relative z-0 hover:z-10 hover:scale-110 transition-transform">
                                  <img 
                                    src={item.displayImage || item.image || "/placeholder.png"} 
                                    className="w-full h-full object-cover" 
                                    onError={(e) => e.target.src="/placeholder.png"}
                                    alt=""
                                  />
                                </div>
                              ))}
                              {orderItems.length > 4 && (
                                <div className="w-12 h-12 rounded-xl border-2 border-white bg-gray-100 flex items-center justify-center text-xs font-bold text-gray-500 z-0">
                                  +{orderItems.length - 4}
                                </div>
                              )}
                           </div>
                         )}
                      </div>
                    </div>
                  </div>

                  {/* === ACTION BAR: If needed === */}
                  {(isPending || canRefund) && !isActionProcessing && (
                    <div className="px-6 py-4 bg-gray-50/80 border-t border-gray-100 flex flex-wrap gap-3 items-center justify-end backdrop-blur-sm">
                      
                      {isPending && tx.personal_app_link && (
                        <a 
                          href={tx.personal_app_link}
                          className="flex-1 sm:flex-none inline-flex justify-center items-center gap-2 px-5 py-2.5 bg-gray-900 hover:bg-black text-white text-sm font-bold rounded-xl transition-all shadow-sm hover:shadow active:scale-95"
                        >
                          <CreditCard className="w-4 h-4" /> Pay Now
                        </a>
                      )}

                      {isPending && (
                        <button
                          onClick={() => handleCancelOrder(order.id)}
                          className="flex-1 sm:flex-none inline-flex justify-center items-center gap-2 px-5 py-2.5 bg-white border border-gray-200 hover:border-red-200 hover:bg-red-50 text-gray-700 hover:text-red-600 text-sm font-bold rounded-xl transition-all active:scale-95"
                        >
                          <Trash2 className="w-4 h-4" /> Cancel
                        </button>
                      )}

                      {canRefund && (
                        <button 
                           onClick={() => handleRefundRequest(order.id)}
                           className="flex-1 sm:flex-none inline-flex justify-center items-center gap-2 px-5 py-2.5 bg-white border border-gray-200 hover:bg-gray-50 text-gray-700 text-sm font-bold rounded-xl transition-all active:scale-95"
                        >
                           <RotateCcw className="w-4 h-4" /> Refund
                        </button>
                      )}
                    </div>
                  )}

                  {/* Loading Indicator for Actions */}
                  {isActionProcessing && (
                     <div className="bg-gray-50 border-t border-gray-100 p-4 flex justify-center">
                        <div className="flex items-center gap-2 text-sm text-gray-500">
                           <Loader className="w-4 h-4 animate-spin" /> Processing request...
                        </div>
                     </div>
                  )}

                  {/* === EXPANDABLE DETAILS === */}
                  <div 
                    onClick={() => toggleDetails(order.id)}
                    className="w-full bg-white border-t border-gray-100 hover:bg-gray-50 transition-colors cursor-pointer"
                  >
                     <div className="px-6 py-3 flex justify-between items-center">
                        <span className="text-xs font-bold text-gray-400 uppercase tracking-widest flex items-center gap-2">
                           <Package className="w-4 h-4" /> Order Details
                        </span>
                        {isExpanded ? (
                           <ChevronDown className="w-5 h-5 text-gray-400" />
                        ) : (
                           <ChevronRight className="w-5 h-5 text-gray-400" />
                        )}
                     </div>

                     {/* Hidden Content */}
                     <div className={`overflow-hidden transition-all duration-300 ease-in-out ${isExpanded ? "max-h-[800px] opacity-100" : "max-h-0 opacity-0"}`}>
                        <div className="px-6 pb-6 pt-2">
                           
                           {/* QR Code Banner for Pending */}
                           {isPending && tx.qr_code && !tx.personal_app_link && (
                              <div className="bg-blue-50 border border-blue-100 rounded-xl p-4 flex items-center gap-4 mb-6">
                                 <div className="bg-white p-2 rounded-lg shadow-sm">
                                    <QrCode className="w-8 h-8 text-blue-600" />
                                 </div>
                                 <div>
                                    <h4 className="font-bold text-blue-900 text-sm">Scan to Pay</h4>
                                    <p className="text-xs text-blue-700 mt-0.5">Use your FIB app to scan this code.</p>
                                 </div>
                              </div>
                           )}

                           {/* Product List Grid */}
                           <div className="space-y-4">
                              {orderItems.map((item, idx) => (
                                 <div key={idx} className="flex gap-4 p-3 rounded-xl border border-gray-50 hover:bg-gray-50 transition-colors">
                                    <div className="w-16 h-16 bg-white rounded-lg border border-gray-100 p-1 flex-shrink-0">
                                       <img src={item.displayImage || item.image} alt="" className="w-full h-full object-contain" />
                                    </div>
                                    <div className="flex-1 min-w-0">
                                       <div className="flex justify-between items-start">
                                          <h4 className="font-bold text-gray-900 text-sm truncate pr-2">{item.name || item.brand}</h4>
                                          <span className="font-semibold text-gray-900 text-sm">{formatPrice(item.price)}</span>
                                       </div>
                                       <p className="text-xs text-gray-500 mt-1">Ref: {item.ref_number || 'N/A'}</p>
                                       <div className="mt-2 flex items-center gap-2">
                                          <span className="text-xs font-medium bg-gray-100 text-gray-600 px-2 py-0.5 rounded">Qty: {item.qty || 1}</span>
                                       </div>
                                    </div>
                                 </div>
                              ))}
                           </div>

                           {/* Delivery Footer */}
                           <div className="mt-6 pt-6 border-t border-gray-100 grid grid-cols-1 md:grid-cols-2 gap-6">
                              <div>
                                 <span className="text-xs font-bold text-gray-400 uppercase tracking-wider block mb-2">Delivery Method</span>
                                 <div className="flex items-center gap-2">
                                    <div className="w-8 h-8 rounded-full bg-indigo-50 flex items-center justify-center text-indigo-600">
                                       <Package className="w-4 h-4" />
                                    </div>
                                    <span className="text-sm font-semibold text-gray-700 capitalize">
                                       {order.delivery_type?.toLowerCase()}
                                    </span>
                                 </div>
                              </div>
                              <div>
                                 <span className="text-xs font-bold text-gray-400 uppercase tracking-wider block mb-2">
                                    {order.delivery_type === 'DELIVERY' ? 'Shipping To' : 'Pickup Location'}
                                 </span>
                                 <div className="flex items-start gap-2">
                                    <div className="w-8 h-8 rounded-full bg-indigo-50 flex items-center justify-center text-indigo-600 shrink-0">
                                       <MapPin className="w-4 h-4" />
                                    </div>
                                    <span className="text-sm text-gray-600 leading-snug pt-1">
                                       {order.delivery_type === 'DELIVERY' ? displayAddress : (order.pick_up_branch || "Branch info unavailable")}
                                    </span>
                                 </div>
                              </div>
                           </div>
                        
                        </div>
                     </div>
                  </div>

                </div>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
}