import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';

/**
 * CollectionsSection - Gents & Ladies collection showcase
 * Features:
 * - Full viewport height split layout
 * - Image carousels for each collection
 * - Action buttons to explore each collection
 * - Elegant hover effects
 */

// Men's collection images
const mensImages = [
  '/Icons and Logos/HomePageMaterials/Collections/Men Images/5db8a2a58a2c946b0dea96fe_continut2.jpg',
  '/Icons and Logos/HomePageMaterials/Collections/Men Images/mens-suit-watch.jpg',
  '/Icons and Logos/HomePageMaterials/Collections/Men Images/Longines-Master-moonphase-1.jpg',
  '/Icons and Logos/HomePageMaterials/Collections/Men Images/tissot-dmarge-article-banner.jpg',
];

// Women's collection images
const womensImages = [
  '/Icons and Logos/HomePageMaterials/Collections/Women Images/083ace924a5742a6b082095e217061d2.jpg',
  '/Icons and Logos/HomePageMaterials/Collections/Women Images/Tissot.jpg',
  '/Icons and Logos/HomePageMaterials/Collections/Women Images/Seiko-Watches-for-Women.jpg',
  '/Icons and Logos/HomePageMaterials/Collections/Women Images/ezgif.com-gif-maker.jpg',
];

const CollectionCard = ({ title, subtitle, images, link, align = 'left' }) => {
  const [currentImage, setCurrentImage] = useState(0);
  const [isHovered, setIsHovered] = useState(false);

  // Auto-advance images
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentImage((prev) => (prev + 1) % images.length);
    }, 4000);
    return () => clearInterval(interval);
  }, [images.length]);

  return (
    <div
      className="relative flex-1 min-h-[50vh] md:min-h-screen overflow-hidden group"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Background Images Carousel */}
      {images.map((img, index) => (
        <div
          key={index}
          className={`absolute inset-0 transition-opacity duration-1000 ${
            index === currentImage ? 'opacity-100' : 'opacity-0'
          }`}
        >
          <img
            src={img}
            alt={`${title} collection`}
            className={`w-full h-full object-cover transition-transform duration-700 ${
              isHovered ? 'scale-110' : 'scale-100'
            }`}
          />
        </div>
      ))}

      {/* Gradient Overlay */}
      <div className={`absolute inset-0 transition-opacity duration-500 ${
        align === 'left'
          ? 'bg-gradient-to-r from-black/80 via-black/40 to-transparent'
          : 'bg-gradient-to-l from-black/80 via-black/40 to-transparent'
      }`} />
      <div className="absolute inset-0 bg-gradient-to-t from-black/60 via-transparent to-transparent" />

      {/* Content */}
      <div className={`absolute inset-0 flex flex-col justify-end p-8 md:p-12 lg:p-16 ${
        align === 'right' ? 'items-end text-right' : 'items-start text-left'
      }`}>
        <p className="text-amber-400 text-xs tracking-[0.3em] uppercase mb-3">{subtitle}</p>
        <h3
          className="text-4xl md:text-5xl lg:text-6xl font-light text-white mb-6 tracking-tight"
          style={{ fontFamily: "'Cormorant Garamond', serif" }}
        >
          {title}
        </h3>
        
        <Link
          to={link}
          className="group/btn inline-flex items-center gap-3 border border-white/30 text-white px-8 py-4 text-sm tracking-widest uppercase transition-all duration-300 hover:bg-white hover:text-black hover:border-white"
        >
          Explore Collection
          <svg
            className="w-4 h-4 transform transition-transform group-hover/btn:translate-x-2"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M17 8l4 4m0 0l-4 4m4-4H3" />
          </svg>
        </Link>

        {/* Image Indicators */}
        <div className={`flex gap-2 mt-8 ${align === 'right' ? 'flex-row-reverse' : ''}`}>
          {images.map((_, index) => (
            <button
              key={index}
              onClick={() => setCurrentImage(index)}
              className={`w-2 h-2 rounded-full transition-all duration-300 ${
                index === currentImage ? 'bg-amber-400 w-6' : 'bg-white/40'
              }`}
              aria-label={`View image ${index + 1}`}
            />
          ))}
        </div>
      </div>

      {/* Decorative Corner */}
      <div className={`absolute top-8 ${align === 'right' ? 'right-8' : 'left-8'} w-16 h-16 border-t border-l border-amber-400/30 ${align === 'right' ? 'transform rotate-90' : ''}`} />
    </div>
  );
};

const CollectionsSection = () => {
  const location = useLocation();
  const isFibMode = location.pathname.startsWith('/fib');
  const basePath = isFibMode ? '/fib' : '';

  return (
    <section className="relative min-h-screen w-full bg-black flex flex-col md:flex-row">
      {/* Gents Collection */}
      <CollectionCard
        title="Gents"
        subtitle="For Him"
        images={mensImages}
        link={`${basePath}/store?gender=Man%2CUnisex`}
        align="left"
      />

      {/* Divider */}
      <div className="hidden md:block w-px bg-gradient-to-b from-transparent via-amber-400/30 to-transparent" />
      <div className="md:hidden h-px bg-gradient-to-r from-transparent via-amber-400/30 to-transparent" />

      {/* Ladies Collection */}
      <CollectionCard
        title="Ladies"
        subtitle="For Her"
        images={womensImages}
        link={`${basePath}/store?gender=Woman%2CUnisex`}
        align="right"
      />
    </section>
  );
};

export default CollectionsSection;


