import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { registerFIBNativeBridge } from "@first-iraqi-bank/sdk/fib-native-bridge";
import { createRoot } from "react-dom/client";
import { ensureFreshVersion } from "./ensureFreshVersion";

registerFIBNativeBridge();
(async () => {
  const canStart = await ensureFreshVersion();
  if (!canStart) return;

  // Optional: unregister any old service workers (if a past build added one)
  if ("serviceWorker" in navigator) {
    try {
      const regs = await navigator.serviceWorker.getRegistrations();
      regs.forEach(r => r.unregister());
    } catch {}
  }

  const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <App />
);
})();



// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();
