import React, { useState, useEffect, useRef } from 'react';
import { Range, getTrackBackground } from 'react-range';

const SimplePriceSlider = ({ min, max, values, onChange }) => {
  const [minInput, setMinInput] = useState(values[0].toString());
  const [maxInput, setMaxInput] = useState(values[1].toString());
  const rangeRef = useRef();

  useEffect(() => {
    setMinInput(values[0].toString());
    setMaxInput(values[1].toString());
  }, [values]);

  const handleInputChange = (e, type) => {
    const rawValue = e.target.value.replace(/,/g, '');
    if (type === 'min') {
      setMinInput(rawValue);
    } else {
      setMaxInput(rawValue);
    }
  };

  const handleInputBlur = () => {
    let newMin = parseInt(minInput.replace(/,/g, ''), 10);
    let newMax = parseInt(maxInput.replace(/,/g, ''), 10);

    if (isNaN(newMin)) newMin = min;
    if (isNaN(newMax)) newMax = max;

    if (newMin < min) newMin = min;
    if (newMax > max) newMax = max;
    if (newMin > newMax) {
      [newMin, newMax] = [newMax, newMin];
    }

    onChange([newMin, newMax]);
  };
  
  const formatNumber = (num) => {
    return num.toLocaleString();
  };


  return (
    <div className="w-full my-4">
      <div className="flex justify-between items-center mb-2">
        <div className="min-price flex-1">
          <input
            type="text"
            value={formatNumber(parseInt(minInput,10))}
            onChange={(e) => handleInputChange(e, 'min')}
            onBlur={handleInputBlur}
            className="w-full p-1 border border-gray-300 rounded text-center"
          />
          <div className="text-sm text-gray-600 text-center">Min</div>
        </div>
        <span className="mx-2">-</span>
        <div className="max-price flex-1">
          <input
            type="text"
            value={formatNumber(parseInt(maxInput,10))}
            onChange={(e) => handleInputChange(e, 'max')}
            onBlur={handleInputBlur}
            className="w-full p-1 border border-gray-300 rounded text-center"
          />
          <div className="text-sm text-gray-600 text-center">Max</div>
        </div>
      </div>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          flexWrap: 'wrap',
          margin: '2em 0'
        }}
      >
        <Range
          ref={rangeRef}
          values={values}
          step={1}
          min={min}
          max={max}
          onChange={(values) => onChange(values)}
          renderTrack={({ props, children }) => (
            <div
              onMouseDown={props.onMouseDown}
              onTouchStart={props.onTouchStart}
              style={{
                ...props.style,
                height: '36px',
                display: 'flex',
                width: '100%'
              }}
            >
              <div
                ref={props.ref}
                style={{
                  height: '5px',
                  width: '100%',
                  borderRadius: '4px',
                  background: getTrackBackground({
                    values: values,
                    colors: ['#ccc', '#000', '#ccc'],
                    min: min,
                    max: max
                  }),
                  alignSelf: 'center'
                }}
              >
                {children}
              </div>
            </div>
          )}
          renderThumb={({ props, isDragged }) => (
            <div
              {...props}
              style={{
                ...props.style,
                height: '20px',
                width: '20px',
                borderRadius: '50%',
                backgroundColor: '#FFF',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
                boxShadow: '0px 2px 6px #AAA'
              }}
            >
              <div
                style={{
                  height: '10px',
                  width: '10px',
                  borderRadius: '50%',
                  backgroundColor: isDragged ? '#000' : '#CCC'
                }}
              />
            </div>
          )}
        />
      </div>
      <div className="flex justify-between mt-2 text-xs text-gray-600">
        <span>{formatNumber(values[0])} IQD</span>
        <span>{formatNumber(values[1])} IQD</span>
      </div>
    </div>
  );
};

export default SimplePriceSlider;