import React, { createContext, useState, useCallback } from 'react';
import { useSearchParams } from 'react-router-dom';
import { fetchStoreProducts } from '../services/productService';

const StoreContext = createContext();

const PAGE_SIZE = 18;

const StoreProvider = ({ children }) => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [products, setProducts] = useState([]);
  const [totalProducts, setTotalProducts] = useState(0);
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);
  const [scrollPosition, setScrollPosition] = useState(0);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [searchQuery, setSearchQuery] = useState(searchParams.get('q') || '');


  const getFiltersFromParams = useCallback(() => {
    const params = Object.fromEntries([...searchParams]);
    return {
      brands: params.brands ? params.brands.split(',') : [],
      gender: params.gender ? params.gender.split(',') : [],
      origin: params.origin ? params.origin.split(',') : [],
      movement: params.movement ? params.movement.split(',') : [],
      category: params.category ? params.category.split(',') : [],
      priceRange: {
        min: params.minPrice ? parseInt(params.minPrice, 10) : 0,
        max: params.maxPrice ? parseInt(params.maxPrice, 10) : 10000000,
      },
      searchQuery: params.q || '',
    };
  }, [searchParams]);

  const loadProducts = useCallback(async (currentPage) => {
    setLoading(true);
    setError(null);
    try {
      const currentFilters = getFiltersFromParams();
      const currentSort = searchParams.get('sort') || 'default';
      
      const { data, error, count } = await fetchStoreProducts({
        page: currentPage,
        filters: currentFilters,
        sortOption: currentSort,
      });

      if (error) throw error;

      setProducts(prev => (currentPage === 1 ? data : [...prev, ...data]));
      if (currentPage === 1) {
        setTotalProducts(count);
      }
      setHasMore(data.length === PAGE_SIZE);
    } catch (err) {
      console.error("Failed to fetch products:", err);
      setError("Could not load products. Please try again later.");
    } finally {
      setLoading(false);
    }
  }, [searchParams, getFiltersFromParams]);

  const value = {
    products,
    setProducts,
    totalProducts,
    page,
    setPage,
    hasMore,
    setHasMore,
    scrollPosition,
    setScrollPosition,
    searchParams,
    setSearchParams,
    loadProducts,
    loading,
    error,
    getFiltersFromParams,
    searchQuery,
    setSearchQuery
  };

  return (
    <StoreContext.Provider value={value}>
      {children}
    </StoreContext.Provider>
  );
};

export { StoreContext, StoreProvider };