import React, { useEffect, useMemo, useRef } from 'react';
import { Check } from 'lucide-react';

export default function AddToCartModal({ isOpen, onClose, onGoToCart, product }) {
  const goToCartButtonRef = useRef(null);

  // Calculate cart count from localStorage
  const cartCount = useMemo(() => {
    const storedCart = localStorage.getItem('fib_cart');
    if (!storedCart) return 0;
    const cart = JSON.parse(storedCart);
    return cart.reduce((sum, item) => sum + item.qty, 0);
  }, [isOpen]); // Recalculate when modal opens

  // ESC key handler
  useEffect(() => {
    const handleEsc = (e) => {
      if (e.key === 'Escape') onClose();
    };

    if (isOpen) {
      window.addEventListener('keydown', handleEsc);
      // Auto-focus "Go to Cart" button for accessibility
      if (goToCartButtonRef.current) {
        goToCartButtonRef.current.focus();
      }
    }

    return () => window.removeEventListener('keydown', handleEsc);
  }, [isOpen, onClose]);

  if (!isOpen) return null;

  return (
    <>
      {/* Backdrop */}
      <div
        className="fixed inset-0 bg-black bg-opacity-50 backdrop-blur-sm z-[100] transition-opacity duration-300"
        onClick={onClose}
        aria-hidden="true"
      />

      {/* Modal - Mobile: Bottom Sheet, Desktop: Centered */}
      <div
        className="fixed inset-0 z-[101] flex items-end md:items-center justify-center pointer-events-none"
        role="dialog"
        aria-modal="true"
        aria-labelledby="modal-title"
      >
        <div
          className="bg-white rounded-t-3xl md:rounded-lg shadow-2xl w-full md:max-w-md md:mx-4 p-6 pointer-events-auto transform transition-all duration-300 ease-out"
          onClick={(e) => e.stopPropagation()}
        >
          {/* Success Icon */}
          <div className="flex justify-center mb-4">
            <div className="w-16 h-16 bg-green-50 rounded-full flex items-center justify-center">
              <Check className="w-10 h-10 text-green-500 animate-bounce" />
            </div>
          </div>

          {/* Title */}
          <h2
            id="modal-title"
            className="text-xl font-bold text-gray-900 text-center mb-2"
          >
            Added to Cart
          </h2>

          {/* Product Info */}
          {product && (
            <div className="text-center mb-3">
              <p className="text-sm text-gray-600">
                {product.brand || 'Product'}
              </p>
              {product.ref_number && (
                <p className="text-xs text-gray-400 font-mono">
                  {product.ref_number}
                </p>
              )}
            </div>
          )}

          {/* Cart Count */}
          <div className="text-center mb-6">
            <p className="text-base font-semibold text-gray-800">
              {cartCount} {cartCount === 1 ? 'item' : 'items'} in cart
            </p>
          </div>

          {/* Action Buttons */}
          <div className="space-y-3">
            {/* Primary: Go to Cart */}
            <button
              ref={goToCartButtonRef}
              onClick={onGoToCart}
              className="w-full bg-gray-900 hover:bg-black text-white py-3 px-6 rounded-lg font-semibold transition-colors duration-150 active:scale-95"
            >
              Go to Cart
            </button>

            {/* Secondary: Continue Shopping */}
            <button
              onClick={onClose}
              className="w-full border-2 border-gray-900 text-gray-900 hover:bg-gray-50 py-3 px-6 rounded-lg font-semibold transition-colors duration-150 active:scale-95"
            >
              Continue Shopping
            </button>
          </div>
        </div>
      </div>
    </>
  );
}
