// Get unique origins for filtering
const origins = [...new Set(brands.map(b => b.origin))].sort();
const categories = [...new Set(brands.map(b => b.category))].sort();

const BrandCard = ({ brand }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  return (
    <div 
      className={`bg-white border border-neutral-200 rounded-lg overflow-hidden transition-all duration-300 hover:shadow-lg hover:border-amber-400/50 ${isExpanded ? 'col-span-1 md:col-span-2' : ''}`}
    >
      <div className="p-6">
        <div className="flex items-start gap-4">
          {/* Logo */}
          <div className="w-20 h-20 flex-shrink-0 bg-neutral-50 rounded-lg p-2 flex items-center justify-center">
            <img 
              src={brand.logo} 
              alt={brand.name} 
              className="max-w-full max-h-full object-contain"
            />
          </div>
          
          {/* Info */}
          <div className="flex-grow min-w-0">
            <div className="flex items-start justify-between">
              <div>
                <h3 className="text-lg font-medium text-neutral-900">{brand.name}</h3>
                <p className="text-neutral-500 text-sm">{brand.origin}  Est. {brand.founded}</p>
              </div>
              <span className="text-xs px-2 py-1 bg-amber-100 text-amber-700 rounded">{brand.category}</span>
            </div>
            
            {/* Expanded History */}
            <div className={`overflow-hidden transition-all duration-300 ${isExpanded ? 'max-h-96 mt-4' : 'max-h-0'}`}>
              <p className="text-neutral-600 text-sm leading-relaxed">{brand.history}</p>
            </div>
          </div>
        </div>

        {/* Actions */}
        <div className="flex items-center justify-between mt-4 pt-4 border-t border-neutral-100">
          <button
            onClick={() => setIsExpanded(!isExpanded)}
            className="text-sm text-neutral-500 hover:text-amber-600 transition-colors flex items-center gap-1"
          >
            {isExpanded ? 'Show less' : 'Read history'}
            <svg 
              className={`w-4 h-4 transition-transform ${isExpanded ? 'rotate-180' : ''}`} 
              fill="none" 
              stroke="currentColor" 
              viewBox="0 0 24 24"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
            </svg>
          </button>
          
          <Link
            to={brand.link}
            className="text-sm font-medium text-amber-600 hover:text-amber-700 transition-colors flex items-center gap-1"
          >
            Shop {brand.name}
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
            </svg>
          </Link>
        </div>
      </div>
    </div>
  );
};

function BrandsPage() {
  const [selectedOrigin, setSelectedOrigin] = useState('all');
  const [selectedCategory, setSelectedCategory] = useState('all');

  const filteredBrands = brands.filter(brand => {
    const matchesOrigin = selectedOrigin === 'all' || brand.origin === selectedOrigin;
    const matchesCategory = selectedCategory === 'all' || brand.category === selectedCategory;
    return matchesOrigin && matchesCategory;
  });

  return (
    <div className="min-h-screen bg-neutral-50">
      {/* Hero Section */}
      <section className="relative py-32 bg-neutral-950 overflow-hidden">
        <div className="absolute inset-0 opacity-20">
          <div className="absolute inset-0" style={{
            backgroundImage: `radial-gradient(circle at 1px 1px, rgba(255,255,255,0.1) 1px, transparent 0)`,
            backgroundSize: '40px 40px'
          }} />
        </div>
        <div className="container mx-auto px-6 md:px-12 lg:px-24 relative z-10">
          <p className="text-amber-400 text-xs tracking-[0.3em] uppercase mb-4">Our Partners</p>
          <h1 
            className="text-4xl md:text-5xl lg:text-6xl font-light text-white tracking-tight mb-6"
            style={{ fontFamily: "'Cormorant Garamond', serif" }}
          >
            Our Brands
          </h1>
          <p className="text-white/60 max-w-2xl text-lg">
            We partner with the world's most prestigious watchmakers to bring you authentic timepieces backed by official warranties and exceptional service.
          </p>
        </div>
      </section>

      {/* Filters */}
      <section className="py-8 bg-white border-b border-neutral-200 sticky top-0 z-20">
        <div className="container mx-auto px-6 md:px-12 lg:px-24">
          <div className="flex flex-wrap items-center gap-4">
            <span className="text-neutral-500 text-sm">Filter by:</span>
            
            {/* Origin Filter */}
            <select
              value={selectedOrigin}
              onChange={(e) => setSelectedOrigin(e.target.value)}
              className="px-4 py-2 border border-neutral-300 rounded-lg text-sm focus:outline-none focus:border-amber-400"
            >
              <option value="all">All Origins</option>
              {origins.map(origin => (
                <option key={origin} value={origin}>{origin}</option>
              ))}
            </select>

            {/* Category Filter */}
            <select
              value={selectedCategory}
              onChange={(e) => setSelectedCategory(e.target.value)}
              className="px-4 py-2 border border-neutral-300 rounded-lg text-sm focus:outline-none focus:border-amber-400"
            >
              <option value="all">All Categories</option>
              {categories.map(category => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>

            <span className="ml-auto text-neutral-400 text-sm">
              {filteredBrands.length} brand{filteredBrands.length !== 1 ? 's' : ''}
            </span>
          </div>
        </div>
      </section>

      {/* Brands Grid */}
      <section className="py-12">
        <div className="container mx-auto px-6 md:px-12 lg:px-24">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {filteredBrands.map((brand) => (
              <BrandCard key={brand.name} brand={brand} />
            ))}
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="py-20 bg-neutral-900">
        <div className="container mx-auto px-6 md:px-12 lg:px-24 text-center">
          <h2 
            className="text-3xl md:text-4xl font-light text-white mb-6"
            style={{ fontFamily: "'Cormorant Garamond', serif" }}
          >
            Find Your Perfect Watch
          </h2>
          <p className="text-white/60 max-w-xl mx-auto mb-8">
            Explore our complete collection of authentic luxury timepieces from all our partner brands.
          </p>
          <Link
            to="/store"
            className="inline-flex items-center gap-3 bg-amber-400 text-black px-8 py-4 font-medium tracking-wide hover:bg-white transition-colors duration-300"
          >
            Browse All Watches
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
            </svg>
          </Link>
        </div>
      </section>
    </div>
  );
}

export default BrandsPage;


