// App.js
import React, { useEffect } from 'react';
import { HashRouter as Router, Routes, Route, useLocation, useNavigate } from 'react-router-dom';
import Header from './components/Header';
import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import StorePage from './pages/StorePage';
import MiniAppLayout from './layouts/MiniAppLayout.js';
import ProductPage from './pages/ProductPage';
import AboutPage from './pages/AboutPage';
import ContactPage from './pages/ContactPage';
import BrandsPage from './pages/BrandsPage';
import { initImageCache } from './services/imageService';
import { ProductProvider } from "./contexts/ProductContext";
import QRAccessPage from './pages/QRPage';
import StoreLayout from './layouts/StoreLayout';
import { StoreProvider } from './contexts/StoreContext.js';
import HeaderFIB from "./components/HeaderFIB";
import { FIBProvider } from './contexts/FIBContext';
import { isFIBEnvironment } from './utils/fibDetection';
import CartPage from './pages/CartPage.js';
import OrdersFIBPage from './pages/OrdersFIBPage.js';

// Auto-redirect to FIB routes when in FIB environment
function useAutoFibLanding() {
  const loc = useLocation();
  const nav = useNavigate();

  useEffect(() => {
    const isBridge = isFIBEnvironment();
    if (!isBridge) return;

    const atFib = (loc.pathname || "").startsWith("/fib");
    if (!atFib) {
      nav("/fib", { replace: true });
    }
  }, [loc, nav]);
}

function FibGuards({ children }) {
  useAutoFibLanding();
  return children;
}

function AppShell() {
  const location = useLocation();
  const isFib = location.pathname.startsWith("/fib");

  return (
    <>
      {!isFib && <Header />}
      <Routes>
        {/* Regular routes */}
        <Route path="/" element={<HomePage />} />
        <Route path="/qr" element={<QRAccessPage />} />
        <Route element={<StoreLayout />}>
          <Route path="/store" element={<StorePage />} />
          <Route path="/product/:productId" element={<ProductPage />} />
          <Route path="/brands" element={<BrandsPage />} />
          <Route path="/about" element={<AboutPage />} />
          <Route path="/contact" element={<ContactPage />} />
        </Route>

        {/* FIB routes */}
        <Route path="/fib" element={<MiniAppLayout />}>
          <Route index element={<><HeaderFIB /><HomePage /></>} />
          <Route element={<StoreLayout />}>
            <Route path="store" element={<><HeaderFIB /><StorePage /></>} />
            <Route path="product/:productId" element={<><HeaderFIB /><ProductPage /></>} />
            <Route path="brands" element={<><HeaderFIB /><BrandsPage /></>} />
            <Route path="about" element={<><HeaderFIB /><AboutPage /></>} />
            <Route path="contact" element={<><HeaderFIB /><ContactPage /></>} />
            <Route path="cart" element={<><HeaderFIB/><CartPage /></>}></Route>
            <Route path="orders" element={<><HeaderFIB/><OrdersFIBPage /></>} />
          </Route>
        </Route>
      </Routes>
      {!isFib && <Footer />}
    </>
  );
}

export default function App() {
  useEffect(() => {
    initImageCache();
  }, []);

  return (
    <Router>
      <FIBProvider>
        <ProductProvider>
          <StoreProvider>
            <FibGuards>
              <AppShell />
            </FibGuards>
          </StoreProvider>
        </ProductProvider>
      </FIBProvider>
    </Router>
  );
}
