import React, { useEffect, useRef, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';

/**
 * GiftGuideSection - Step-by-step gift process visualization
 * Features:
 * - Full viewport height
 * - Interactive step indicators
 * - Visual process flow
 * - Elegant animations on scroll
 */

const giftSteps = [
  {
    number: '01',
    title: 'Select Product',
    description: 'Browse our curated collection and choose the perfect timepiece for your loved one.',
    icon: (
      <svg className="w-12 h-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
      </svg>
    ),
  },
  {
    number: '02',
    title: 'Select Wrap',
    description: 'Choose from our premium gift wrapping options for an unforgettable presentation.',
    icon: (
      <svg className="w-12 h-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M12 8v13m0-13V6a2 2 0 112 2h-2zm0 0V5.5A2.5 2.5 0 109.5 8H12zm-7 4h14M5 12a2 2 0 110-4h14a2 2 0 110 4M5 12v7a2 2 0 002 2h10a2 2 0 002-2v-7" />
      </svg>
    ),
  },
  {
    number: '03',
    title: 'Place Order',
    description: 'Complete your purchase with secure payment and fast processing.',
    icon: (
      <svg className="w-12 h-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
      </svg>
    ),
  },
  {
    number: '04',
    title: 'Receive & Celebrate',
    description: 'Your gift arrives beautifully packaged, ready to create lasting memories.',
    icon: (
      <svg className="w-12 h-12" fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1} d="M5 3v4M3 5h4M6 17v4m-2-2h4m5-16l2.286 6.857L21 12l-5.714 2.143L13 21l-2.286-6.857L5 12l5.714-2.143L13 3z" />
      </svg>
    ),
  },
];

const StepCard = ({ step, index, isVisible }) => {
  return (
    <div
      className={`relative flex flex-col items-center text-center transition-all duration-700 ${
        isVisible ? 'opacity-100 translate-y-0' : 'opacity-0 translate-y-8'
      }`}
      style={{ transitionDelay: `${index * 150}ms` }}
    >
      {/* Step Number - Background */}
      <div className="absolute -top-4 left-1/2 -translate-x-1/2 text-8xl font-bold text-white/5 select-none pointer-events-none"
        style={{ fontFamily: "'Cormorant Garamond', serif" }}
      >
        {step.number}
      </div>

      {/* Icon Container */}
      <div className="relative z-10 w-24 h-24 rounded-full border border-amber-400/30 flex items-center justify-center text-amber-400 mb-6 bg-neutral-950/50 backdrop-blur-sm transition-all duration-300 hover:border-amber-400 hover:bg-amber-400/10">
        {step.icon}
      </div>

      {/* Content */}
      <h4 className="text-xl font-light text-white mb-3 tracking-wide">{step.title}</h4>
      <p className="text-white/50 text-sm leading-relaxed max-w-[200px]">{step.description}</p>

      {/* Connector Line (not on last item) */}
      {index < giftSteps.length - 1 && (
        <div className="hidden lg:block absolute top-12 left-[calc(50%+60px)] w-[calc(100%-60px)] h-px">
          <div className="h-full bg-gradient-to-r from-amber-400/30 to-amber-400/10" />
          <div className="absolute right-0 top-1/2 -translate-y-1/2 w-2 h-2 rotate-45 border-r border-t border-amber-400/30" />
        </div>
      )}
    </div>
  );
};

const GiftGuideSection = () => {
  const sectionRef = useRef(null);
  const [isVisible, setIsVisible] = useState(false);
  const location = useLocation();
  const isFibMode = location.pathname.startsWith('/fib');
  const basePath = isFibMode ? '/fib' : '';

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.2 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, []);

  return (
    <section
      ref={sectionRef}
      className="relative min-h-screen w-full bg-neutral-950 flex flex-col justify-center overflow-hidden py-20"
    >
      {/* Background Elements */}
      <div className="absolute inset-0">
        {/* Subtle radial gradient */}
        <div className="absolute inset-0 bg-[radial-gradient(ellipse_at_center,rgba(251,191,36,0.03)_0%,transparent_70%)]" />
        
        {/* Decorative circles */}
        <div className="absolute top-1/4 left-1/4 w-96 h-96 border border-white/5 rounded-full" />
        <div className="absolute bottom-1/4 right-1/4 w-64 h-64 border border-amber-400/10 rounded-full" />
      </div>

      {/* Header */}
      <div className="relative z-10 text-center mb-20 px-8">
        <p className="text-amber-400 text-xs tracking-[0.3em] uppercase mb-4">The Perfect Present</p>
        <h2
          className="text-4xl md:text-5xl lg:text-6xl font-light text-white tracking-tight mb-6"
          style={{ fontFamily: "'Cormorant Garamond', serif" }}
        >
          Gift Guide
        </h2>
        <p className="text-white/50 max-w-xl mx-auto leading-relaxed">
          Make every moment memorable with a timepiece gift. Follow our simple steps to create the perfect surprise.
        </p>
      </div>

      {/* Steps Grid */}
      <div className="relative z-10 grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-12 lg:gap-8 px-8 md:px-16 lg:px-24 max-w-7xl mx-auto">
        {giftSteps.map((step, index) => (
          <StepCard key={index} step={step} index={index} isVisible={isVisible} />
        ))}
      </div>

      {/* CTA */}
      <div className="relative z-10 text-center mt-20 px-8">
        <Link
          to={`${basePath}/store`}
          className="inline-flex items-center gap-3 bg-amber-400 text-black px-10 py-4 text-sm font-medium tracking-widest uppercase transition-all duration-300 hover:bg-white"
        >
          Start Shopping
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
          </svg>
        </Link>
      </div>

      {/* Decorative Line */}
      <div className="absolute bottom-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-amber-400/30 to-transparent" />
    </section>
  );
};

export default GiftGuideSection;


