import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import { useParams, useLocation, useNavigate } from "react-router-dom";
import { supabase } from "../supabaseClient";
import { unmapCharacters } from "../characterMapping";
import ProductCard from "../components/ProductCard";
import AddToCartModal from "../components/AddToCartModal";
import { useFIB } from "../contexts/FIBContext";
import { ShoppingCart, Loader2, Check } from "lucide-react";

function ProductPage() {
  const { productId } = useParams();
  const location = useLocation();
  const { addToCartLocal } = useFIB();
  const navigate = useNavigate();

  const [product, setProduct] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const [similarProducts, setSimilarProducts] = useState([]);
  
  // New state for button animation
  const [addState, setAddState] = useState('idle'); // 'idle' | 'adding' | 'added'

  // State for add-to-cart modal
  const [showCartModal, setShowCartModal] = useState(false);

  const cleanProductId = unmapCharacters(productId);
  const isFib = location.pathname.startsWith("/fib");

  useEffect(() => {
    console.log("Product ID from URL:", productId);
    console.log("Cleaned Product ID:", cleanProductId);
    const fetchProduct = async () => {
      setLoading(true);
      setProduct(null);
      try {
        const { data, error } = await supabase
          .from('products')
          .select('*')
          .or(`ref_number.eq.${cleanProductId},sku.eq.${cleanProductId}`)
          .single();

        if (error) {
          if (error.code === 'PGRST116') {
             console.log("Product not found in Supabase with ID:", cleanProductId);
          } else {
            throw error;
          }
        }
        
        setProduct(data);

        if (data && data.similar_products_refs && data.similar_products_refs.length > 0) {
          fetchSimilarProducts(data.similar_products_refs);
        }

      } catch (error) {
        console.error("Error fetching product from Supabase:", error);
        setProduct(null);
      } finally {
        setLoading(false);
      }
    };

    fetchProduct();
  }, [cleanProductId, productId]);

  const fetchSimilarProducts = async (similarRefsArray) => {
    try {
      if (!similarRefsArray || similarRefsArray.length === 0) return;
      const refsToFetch = similarRefsArray.slice(0, 4);
      const { data, error } = await supabase
        .from('products')
        .select('*')
        .in('ref_number', refsToFetch);
      if (error) throw error;
      setSimilarProducts(data || []);
    } catch (error) {
      console.error("Error fetching similar products from Supabase:", error);
    }
  };

  const handleAddToCart = async () => {
    if (addState !== 'idle' || !product) return;

    setAddState('adding');
    await addToCartLocal(product);
    setAddState('added');
    setShowCartModal(true); // Show the modal

    setTimeout(() => {
      setAddState('idle');
    }, 2000);
  };

  if (loading) {
    return <div className="container mx-auto p-4 mt-24">Loading product details...</div>;
  }

  if (!product) {
    return <div className="container mx-auto p-4 mt-24">Product not found.</div>;
  }

  const handleImageClick = () => setShowModal(true);
  const closeModal = () => setShowModal(false);
  const handleBackdropClick = (e) => {
    if (e.target.id === "backdrop") closeModal();
  };

  const formatPrice = (p) => p ? `${Number(p).toLocaleString()} IQD` : "No Price";

  const {
    brand = "Unknown Brand",
    ref_number = "No Reference",
    description = "",
    gender = "",
    origin = "",
    price = 0,
    discounted_price = 0,
    image_urls = {},
    attributes = {}
  } = product;

  const mainImageUrl = image_urls.large || image_urls.main || '/path/to/default-image.jpg';
  const otherImageUrls = image_urls.others || [];
  const allImages = [mainImageUrl, ...otherImageUrls];

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
  };

  const specifications = Object.entries(attributes).map(([label, value]) => ({ label, value }));

  const isOutOfStock = (
    (product.muthana_qty || 0) === 0 &&
    (product.royal_qty || 0) === 0 &&
    (product.wh_qty || 0) === 0
  );

  const handleWhatsAppClick = () => {
    const phoneNumber = "9647511151003";
    const productPageUrl = window.location.href;
    const message = ` ,   \n${brand} ${ref_number}\n\n \n${productPageUrl}`;
    const whatsappUrl = `https://wa.me/${phoneNumber}?text=${encodeURIComponent(message)}`;
    window.open(whatsappUrl, "_blank");
  };

  return (
    <div className="container mx-auto px-4 mt-24">
      <div className="flex flex-col md:flex-row items-start overflow-hidden">
        <div className="md:w-3/5 w-full p-4 grid grid-cols-1 justify-center items-start">
          <div className="flex justify-center items-start relative">
            <img
              src={mainImageUrl}
              alt={description || brand}
              className="h-[calc(50vh-40px)] object-contain md:p-10 cursor-pointer"
              onClick={handleImageClick}
            />
          </div>
          {otherImageUrls.length > 0 && (
            <div className="grid grid-cols-2 gap-4 mt-4">
              {otherImageUrls.map((img, index) => (
                <img
                  key={index}
                  src={img}
                  alt={`Additional Image ${index + 1}`}
                  className="w-full object-cover cursor-pointer"
                  onClick={handleImageClick}
                />
              ))}
            </div>
          )}
        </div>

        <div className="md:w-2/5 w-full p-4 mt-10 md:mt-24 border rounded-lg">
          <div className="p-6">
            <div className="flex items-center justify-center">
            {isOutOfStock && (
                <span className="text-white font-bold text-xl px-6 py-3 bg-red-600 rounded-lg shadow-lg items-center justify-center">
                  OUT OF STOCK
                </span>
            )}</div>
            <br></br>
            <h1 className="text-2xl font-extrabold mb-2">{brand}</h1>
            <h2 className="text-lg font-semibold text-gray-500 mb-4">{ref_number}</h2>
            <p className="text-xl text-start mb-4 font-bold">
              {discounted_price > 0 && discounted_price < price ? (
                <span>
                  <span className="text-red-500 line-through mr-2">{formatPrice(price)}</span>
                  <span>{formatPrice(discounted_price)}</span>
                </span>
              ) : (
                <span>{formatPrice(price)}</span>
              )}
            </p>
            <hr className="my-6" />
            {description && <p className="text-gray-700 mb-6">{description}</p>}
            
            <div className="space-y-4">
              <div className="flex justify-between">
                <span className="font-bold text-gray-600">Origin</span>
                <span className="text-gray-800">{origin}</span>
              </div>
              {gender && (
                <div className="flex justify-between">
                  <span className="font-bold text-gray-600">Gender</span>
                  <span className="text-gray-800">{gender}</span>
                </div>
              )}
            </div>

            {specifications.length > 0 && <hr className="my-6" />}

            <div className="space-y-2">
              {specifications.map((spec, index) => (
                <div key={index} className={`flex justify-between p-2 rounded-md ${index % 2 === 0 ? 'bg-gray-50' : ''}`}>
                  <span className="font-semibold text-gray-600">{spec.label}</span>
                  <span className="text-gray-800">{spec.value}</span>
                </div>
              ))}
            </div>
            
            {/* Conditional Add to Cart Button */}
            {isFib && (
              <button
                onClick={handleAddToCart}
                disabled={addState !== 'idle' || isOutOfStock}
                className={`
                  mt-6 w-full py-3 px-4 rounded-lg font-bold text-lg transition-all duration-300 ease-in-out flex items-center justify-center gap-2
                  ${isOutOfStock ? 'bg-gray-300 text-gray-500 cursor-not-allowed' : 
                    addState === 'added' 
                      ? 'bg-gray-800 text-green-400' 
                      : 'bg-gray-900 text-white hover:bg-black hover:shadow-lg active:scale-95'
                  }
                `}
              >
                {addState === 'idle' && (
                  <>
                    <ShoppingCart className="w-5 h-5" />
                    <span>{isOutOfStock ? 'Out of Stock' : 'Add to Cart'}</span>
                  </>
                )}
                {addState === 'adding' && (
                  <>
                    <Loader2 className="w-5 h-5 animate-spin" />
                    <span>Adding to Cart...</span>
                  </>
                )}
                {addState === 'added' && (
                  <>
                    <Check className="w-5 h-5 animate-bounce" />
                    <span>Successfully Added</span>
                  </>
                )}
              </button>
            )}

           {!isFib && (
             <button 
              onClick={handleWhatsAppClick}
              className="mt-4 w-full bg-green-500 hover:bg-green-600 text-white font-bold py-3 px-4 rounded-lg transition duration-300 ease-in-out flex items-center justify-center"
            >
              <img src="/Icons and Logos/whatsapp.svg" alt="WhatsApp" className="w-6 h-6 mr-2" />
              Contact us on WhatsApp
            </button>
            )}
          </div>
        </div>
      </div>

      {similarProducts.length > 0 && (
        <div className="w-full mt-12 px-4">
          <h2 className="text-2xl font-bold mb-6">Similar Products</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {similarProducts.map((similarProduct) => {
              const outOfStock = (similarProduct.muthana_qty || 0) === 0 && (similarProduct.royal_qty || 0) === 0 && (similarProduct.wh_qty || 0) === 0;
              const productWithStock = { ...similarProduct, outOfStock };
              return <ProductCard key={similarProduct.ref_number} product={productWithStock} />;
            })}
          </div>
        </div>
      )}

      {showModal && (
        <div className="fixed inset-0 overflow-hidden z-[200]" id="backdrop" onClick={handleBackdropClick}>
          <div className="flex justify-center items-center h-screen w-screen">
            <div className="relative p-2 rounded bg-white w-full max-w-4xl mx-auto">
              <Slider {...settings}>
                {allImages.map((img, index) => (
                  <div key={index} className="flex justify-center items-center">
                    <img src={img} alt={`Product Image ${index + 1}`} className="object-contain h-screen" />
                  </div>
                ))}
              </Slider>
              <button className="absolute top-6 right-6 text-2xl p-2 text-black" onClick={closeModal}>
                &times;
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Add to Cart Modal */}
      <AddToCartModal
        isOpen={showCartModal}
        onClose={() => setShowCartModal(false)}
        onGoToCart={() => {
          setShowCartModal(false);
          navigate('/fib/cart');
        }}
        product={product}
      />
    </div>
  );
}

export default ProductPage;