import { supabase } from '../supabaseClient';

const PRODUCTS_PER_PAGE = 18;

/**
 * Fetches a paginated, filtered, and sorted list of products for the store page.
 * @param {object} options - Filtering, sorting, and pagination options.
 * @param {number} options.page - The current page number (1-indexed) for pagination.
 * @param {object} options.filters - An object containing active filter criteria.
 * @param {string} options.sortOption - The current sort option (e.g., 'price-asc').
 * @returns {Promise<{data: object[], error: object|null, count: number|null}>}
 */
export const fetchStoreProducts = async ({ page = 1, filters, sortOption }) => {
  let query = supabase
    .from('products')
    .select('*', { count: 'exact' })
    .eq('visibility', true)
    .not('image_urls', 'is', null)
    .not('image_urls->>main', 'eq', '');

  // --- APPLY SEARCH ---
  if (filters && filters.searchQuery) {
    query = query.or(`ref_number.ilike.%${filters.searchQuery}%,product_name.ilike.%${filters.searchQuery}%`);
  }

  // --- APPLY FILTERS ---
  if (filters) {
    if (filters.brands && filters.brands.length > 0) {
      query = query.in('brand', filters.brands);
    }
    if (filters.gender && filters.gender.length > 0) {
      const genderFilters = [...filters.gender];
      if (genderFilters.includes('Man') || genderFilters.includes('Woman')) {
        if (!genderFilters.includes('Unisex')) {
          genderFilters.push('Unisex');
        }
      }
      query = query.in('gender', genderFilters);
    }
    if (filters.origin && filters.origin.length > 0) {
      query = query.in('origin', filters.origin);
    }
    if (filters.movement && filters.movement.length > 0) {
      const movementConditions = filters.movement.map(m => `attributes->>Movement.ilike.%${m}%`).join(',');
      query = query.or(movementConditions);
    }
    if (filters.category && filters.category.length > 0) {
      query = query.in('wh_category', filters.category);
    }
    if (filters.priceRange) {
      if (filters.priceRange.min > 0) {
        query = query.gte('price', filters.priceRange.min);
      }
      if (filters.priceRange.max < 100000000) {
        query = query.lte('price', filters.priceRange.max);
      }
    }
  }

  // --- APPLY SORTING ---
  // --- FIX: Ensured sorting logic correctly targets discounted_price ---
  if (sortOption) {
    switch (sortOption) {
      case 'price-asc':
        // --- FIX: Changed to sort by 'price' column ---
        query = query.order('price', { ascending: true });
        break;
      case 'price-desc':
        // --- FIX: Changed to sort by 'price' column ---
        query = query.order('price', { ascending: false });
        break;
      case 'ref-asc':
        query = query.order('ref_number', { ascending: true });
        break;
      case 'ref-desc':
        query = query.order('ref_number', { ascending: false });
        break;
      default:
        query = query.order('ref_number', { ascending: true });
        break;
    }
  }

  // --- APPLY PAGINATION ---
  const startIndex = (page - 1) * PRODUCTS_PER_PAGE;
  const endIndex = startIndex + PRODUCTS_PER_PAGE - 1;
  query = query.range(startIndex, endIndex);

  const { data, error, count } = await query;

  if (error) {
    console.error('Error fetching store products:', error);
  }

  return { data, error, count };
};