  const setSession = (sessionData) => {
    if (!sessionData || !sessionData.user) {
      clearSession();
      return;
    }

    const newSession = {
      user: sessionData.user,
      token: sessionData.token,
      timestamp: Date.now()
    };

    setSessionState(newSession);
    localStorage.setItem(STORAGE_KEY, JSON.stringify(newSession));
  };

