    // 2. Check if item exists
    const existingItemIndex = currentCart.findIndex(
      (item) => String(item.product_id) === String(productId)
    );

    // 3. Update or Push
    if (existingItemIndex > -1) {
      currentCart[existingItemIndex].qty += 1;
    } else {
      currentCart.push({ product_id: productId, qty: 1 });
    }

    // 4. Save back to Local Storage
    localStorage.setItem('fib_cart', JSON.stringify(currentCart));
    
    console.log(`[addToCart] Added ${productId} to local cart`);
    
    // Optional: If you have a state setter exposed in context, call it here to update UI immediately
    // setCartCount(currentCart.length); 

