import { supabase } from '../supabaseClient';

/**
 * Sends a contact form email via Supabase Edge Function
 * @param {Object} contactData - Contact form data
 * @param {string} contactData.name - Customer's name
 * @param {string} contactData.phone - Customer's phone number
 * @param {string} contactData.email - Customer's email address
 * @param {string} contactData.message - Customer's message
 * @returns {Promise<Object>} Response from server
 * @throws {Error} If email sending fails
 */
export async function sendContactEmail(contactData) {
  // Call Supabase Edge Function
  const { data, error } = await supabase.functions.invoke('send-contact-email', {
    method: 'POST',
    body: {
      name: contactData.name,
      phone: contactData.phone,
      email: contactData.email,
      message: contactData.message
    }
  });

  // Handle errors
  if (error) {
    console.error('Email service error:', error);
    throw new Error(error.message || 'Failed to send email');
  }

  return data;
}
