// ensureFreshVersion.js
export async function ensureFreshVersion() {
  const KEY = "WH_APP_VERSION";
  try {
    // Hard-bust caches for this request
    const res = await fetch(`/version.json?cb=${Date.now()}`, { cache: "no-store" });
    if (!res.ok) return true;
    const { version } = await res.json();
    if (!version) return true;

    const prev = localStorage.getItem(KEY);
    if (prev && prev !== version) {
      localStorage.setItem(KEY, version);
      // Hard reload the HTML with a version param so index.html itself refreshes
      const url = new URL(window.location.href);
      url.searchParams.set("v", version);
      window.location.replace(url.toString());
      return false; // stop further init; were reloading
    }
    localStorage.setItem(KEY, version);
  } catch (_) {}
  return true;
}
