import React, { useState, useEffect } from 'react';
import { Link, useLocation } from 'react-router-dom';
import Slider from 'react-slick';
import { supabase } from '../../supabaseClient';
import { mapCharacters } from '../../characterMapping';

/**
 * FeaturedProductsSection - Full-screen featured products showcase
 * Features:
 * - Full viewport height
 * - Product carousel with white cards
 * - Title positioned at bottom-left
 * - Elegant product cards
 */

// Featured product references - easily configurable
const FEATURED_REFS = [
  "T120.410.11.051.00",
  "1514083",
  "A 84936 AA04",
  "BJ7007-02L",
  "T150.410.11.091.00"
];

const FeaturedProductCard = ({ product, basePath = '' }) => {
  const imageUrl = product.image_urls?.medium || product.image_urls?.main || '/placeholder.jpg';
  const productLink = `${basePath}/product/${mapCharacters(product.ref_number)}`;

  const formatPrice = (price) => {
    return price ? `${Number(price).toLocaleString()} IQD` : '';
  };

  return (
    <Link to={productLink} className="group block px-3 py-2">
      <div className="relative bg-white rounded-lg overflow-hidden shadow-lg transition-all duration-500 hover:shadow-2xl hover:-translate-y-2">
        {/* Product Image */}
        <div className="aspect-square p-6 flex items-center justify-center bg-white">
          <img
            src={imageUrl}
            alt={product.brand}
            className="max-h-full max-w-full object-contain transition-transform duration-700 group-hover:scale-110"
          />
        </div>
        
        {/* Product Info */}
        <div className="p-5 border-t border-neutral-100 bg-white">
          <p className="text-amber-600 text-xs tracking-widest uppercase mb-2">{product.brand}</p>
          <h3 className="text-neutral-900 font-medium text-base mb-2 truncate">{product.ref_number}</h3>
          <p className="text-neutral-600 text-sm">
            {product.discounted_price && product.discounted_price < product.price ? (
              <>
                <span className="line-through mr-2 text-neutral-400">{formatPrice(product.price)}</span>
                <span className="text-amber-600 font-medium">{formatPrice(product.discounted_price)}</span>
              </>
            ) : (
              formatPrice(product.price)
            )}
          </p>
        </div>

        {/* Hover Accent */}
        <div className="absolute top-0 left-0 w-full h-1 bg-amber-400 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300 origin-left" />
      </div>
    </Link>
  );
};

// Placeholder card for loading/empty states
const PlaceholderCard = () => (
  <div className="px-3">
    <div className="bg-white rounded-lg overflow-hidden shadow-lg">
      <div className="aspect-square p-6 flex items-center justify-center bg-neutral-50">
        <div className="w-32 h-32 bg-neutral-200 rounded-full animate-pulse" />
      </div>
      <div className="p-5 border-t border-neutral-100">
        <div className="h-3 w-16 bg-neutral-200 rounded mb-3 animate-pulse" />
        <div className="h-5 w-32 bg-neutral-100 rounded mb-2 animate-pulse" />
        <div className="h-4 w-24 bg-neutral-100 rounded animate-pulse" />
      </div>
    </div>
  </div>
);

const FeaturedProductsSection = () => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const location = useLocation();
  const isFibMode = location.pathname.startsWith('/fib');
  const basePath = isFibMode ? '/fib' : '';

  useEffect(() => {
    const fetchFeaturedProducts = async () => {
      try {
        const { data, error } = await supabase
          .from('products')
          .select('*')
          .in('ref_number', FEATURED_REFS);

        if (error) throw error;
        setProducts(data || []);
      } catch (error) {
        console.error("Error fetching featured products:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchFeaturedProducts();
  }, []);

  const sliderSettings = {
    dots: false,
    infinite: true,
    speed: 600,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 4000,
    pauseOnHover: true,
    arrows: false,
    responsive: [
      { breakpoint: 1280, settings: { slidesToShow: 3 } },
      { breakpoint: 1024, settings: { slidesToShow: 2 } },
      { breakpoint: 640, settings: { slidesToShow: 1, centerMode: true, centerPadding: '40px' } },
    ],
  };

  return (
    <section className="relative min-h-screen w-full bg-neutral-100 flex flex-col justify-end">
      {/* Subtle Background Pattern */}
      <div className="absolute inset-0 opacity-50">
        <div className="absolute inset-0" style={{
          backgroundImage: `radial-gradient(circle at 1px 1px, rgba(0,0,0,0.03) 1px, transparent 0)`,
          backgroundSize: '40px 40px'
        }} />
      </div>

      {/* Products Carousel */}
      <div className="relative z-10 w-full px-4 md:px-8 lg:px-16 mb-8 py-4 overflow-visible">
        {loading ? (
          <div className="flex gap-6">
            {[1, 2, 3, 4].map((i) => <PlaceholderCard key={i} />)}
          </div>
        ) : products.length > 0 ? (
          <div className="slider-overflow-visible">
            <Slider {...sliderSettings}>
              {products.map((product) => (
                <FeaturedProductCard key={product.ref_number} product={product} basePath={basePath} />
              ))}
            </Slider>
          </div>
        ) : (
          // Fallback placeholders when no products
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
            {[1, 2, 3, 4].map((i) => <PlaceholderCard key={i} />)}
          </div>
        )}
      </div>
      {/* Mobile-friendly centered "View All" button */}
      <div className="relative z-10 w-full flex justify-center mb-6 md:hidden">
        <Link
          to={`${basePath}/store`}
          className="inline-flex items-center gap-2 px-5 py-2.5 rounded-full bg-amber-600 text-white shadow-sm hover:bg-amber-700 transition-colors duration-300 focus:outline-none focus:ring-2 focus:ring-amber-400"
        >
          View All
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M17 8l4 4m0 0l-4 4m4-4H3" />
          </svg>
        </Link>
      </div>

      {/* Bottom Left Title */}
      <div className="relative z-10 p-8 md:p-16 lg:p-24">
        <div className="flex items-end justify-between">
          <div>
            <p className="text-amber-600 text-xs tracking-[0.3em] uppercase mb-4">Curated Selection</p>
            <h2
              className="text-4xl md:text-5xl lg:text-6xl font-light text-neutral-900 tracking-tight"
              style={{ fontFamily: "'Cormorant Garamond', serif" }}
            >
              Featured Products
            </h2>
          </div>
          
          <Link
            to={`${basePath}/store`}
            className="hidden md:inline-flex items-center gap-3 text-neutral-500 hover:text-amber-600 transition-colors duration-300 text-sm tracking-widest uppercase"
          >
            View All
            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M17 8l4 4m0 0l-4 4m4-4H3" />
            </svg>
          </Link>
        </div>
      </div>

      {/* Decorative Line */}
      <div className="absolute bottom-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-amber-400/50 to-transparent" />
    </section>
  );
};

export default FeaturedProductsSection;
