// src/components/ProductCard.js
import React, { useState } from "react";
import { createPortal } from "react-dom";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { mapCharacters } from "../characterMapping";
import AddToCartModal from "./AddToCartModal";
import { useFIB } from "../contexts/FIBContext";
import { ShoppingCart, Loader2, Check } from "lucide-react";

const DEFAULT_IMG = "/placeholder.png"; 

const ProductCard = ({ id, product, isLoading, priority }) => {
  const location = useLocation();
  const navigate = useNavigate();
  const { addToCartLocal } = useFIB();
  const [addState, setAddState] = useState('idle'); // 'idle' | 'adding' | 'added'
  const [showCartModal, setShowCartModal] = useState(false);

  if (isLoading) {
    return (
      <div className="border p-4 rounded-lg shadow-lg animate-pulse">
        <div className="w-full h-48 bg-gray-300 rounded" />
        <div className="mt-4 h-6 bg-gray-300 rounded w-3/4" />
        <div className="mt-2 h-4 bg-gray-300 rounded w-1/2" />
      </div>
    );
  }

  if (!product) return null;

  const {
    ref_number,
    brand,
    price,
    discounted_price,
    image_urls,
    outOfStock,
  } = product;

  const mappedReference = mapCharacters(ref_number || id || "");
  const imageUrl =
    image_urls?.thumbnail ||
    image_urls?.medium ||
    image_urls?.main ||
    DEFAULT_IMG;

  const formatPrice = (p) =>
    p ? `${Number(p).toLocaleString()} IQD` : "No Price";

  const isFib = location.pathname.startsWith("/fib");
  
  const linkTo = isFib 
    ? `/fib/product/${mappedReference}` 
    : `/product/${mappedReference}`;

  const handleAddToCart = async (e) => {
    e.preventDefault(); // Prevent Link navigation
    e.stopPropagation(); // Prevent bubbling
    
    if (addState !== 'idle' || !product) return;

    setAddState('adding');
    
    // Wait for the DB operation
    await addToCartLocal(product);
    
    // Show success state
    setAddState('added');
    setShowCartModal(true); // Show the modal

    // Reset after 2 seconds
    setTimeout(() => {
      setAddState('idle');
    }, 2000);
  };

  return (
    <>
     <Link to={linkTo} className="block h-full">
      <div className="group block border rounded-lg overflow-hidden shadow-sm hover:shadow-xl transition-shadow duration-300 h-full flex flex-col bg-white">
        <div className="relative flex-grow flex items-center justify-center bg-white">
          <img
            src={imageUrl}
            alt={`${brand} - ${ref_number}`}
            className="w-full h-48 object-contain transition-transform duration-300 group-hover:scale-105 p-2"
            loading={priority ? 'eager' : 'lazy'}
          />
          {outOfStock && (
            <div className="absolute inset-0 bg-black bg-opacity-10 flex items-center justify-center">
            </div>
          )}
        </div>
        <div className="p-4 bg-gray-50 flex flex-col flex-grow">
          <h3 className="text-lg font-semibold truncate">{brand || 'No Brand'}</h3>
          <p className="text-sm text-gray-600">{ref_number || 'No Refs'}</p>
          <div className="mt-2 text-lg font-bold mb-2">
            {discounted_price > 0 && discounted_price < price ? (
              <div>
                <span className="text-red-500 line-through text-sm">{formatPrice(price)}</span>{' '}
                <span>{formatPrice(discounted_price)}</span>
              </div>
            ) : (
              <span>{formatPrice(price)}</span>
            )}
            {outOfStock &&(
            <div>
              <span className="text-white font-bold text-sm bg-red-600 px-2 py-1 rounded">OUT OF STOCK</span>
            </div>)}
          </div>
          
          <div className="flex-grow"></div>

          {/* Animated Add to Cart Button */}
          {isFib && (
            <button 
              onClick={handleAddToCart}
              disabled={addState !== 'idle' || outOfStock}
              className={`
                mt-2 w-full py-2.5 px-4 rounded-lg font-semibold z-10 relative flex items-center justify-center gap-2 transition-all duration-300
                ${outOfStock ? 'bg-gray-300 text-gray-500 cursor-not-allowed' : 
                  addState === 'added' 
                    ? 'bg-gray-800 text-green-400' 
                    : 'bg-gray-900 text-white hover:bg-black hover:shadow-lg active:scale-95'
                }
              `}
            >
              {addState === 'idle' && (
                <>
                  <ShoppingCart className="w-4 h-4" />
                  <span>{outOfStock ? 'Out of Stock' : 'Add to Cart'}</span>
                </>
              )}
              {addState === 'adding' && (
                <>
                  <Loader2 className="w-4 h-4 animate-spin" />
                  <span>Adding...</span>
                </>
              )}
              {addState === 'added' && (
                <>
                  <Check className="w-4 h-4 animate-bounce" />
                  <span>Added</span>
                </>
              )}
            </button>
          )}
        </div>
      </div>
    </Link>

    {/* Add to Cart Modal - Using Portal to escape Link context */}
    {showCartModal && createPortal(
      <AddToCartModal
        isOpen={showCartModal}
        onClose={() => setShowCartModal(false)}
        onGoToCart={() => {
          setShowCartModal(false);
          navigate('/fib/cart');
        }}
        product={product}
      />,
      document.body
    )}
  </>
  );
};

export default ProductCard;